import React from 'react';
import { BannerBlend as BannerBlendBase } from './BannerBlend';

export const BannerBlend = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BannerBlendBase, props)
  );
};
