import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/grid-sections.css';

/**
 * MosaicCard Component - Clean, modern card for grid layouts
 */
export const MosaicCard = ({ data, className, loader = false }) => {
  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
    setShowLoader(false);
  }, [loader]);

  if (showLoader) {
    return (
      <div className={clsx('universal-card p-0 h-full', className)}>
        <div className="skeleton-box w-full aspect-video rounded-b-none"></div>
        <div className="p-6 space-y-3">
          <div className="skeleton-box w-20 h-4"></div>
          <div className="skeleton-box w-3/4 h-7"></div>
          <div className="skeleton-box w-full h-4"></div>
          <div className="skeleton-box w-full h-4"></div>
        </div>
      </div>
    );
  }

  const {
    title,
    description,
    image,
    category,
    theme = 'light',
    mode = 'default', // 'default' | 'overlay'
  } = data;

  const isOverlay = mode === 'overlay';

  return (
    <div className={clsx(
      'universal-card h-full group', 
      isOverlay && 'universal-card-overlay',
      className
    )}>
      {image && (
        <div className={clsx(
          'universal-card-image-container',
          !isOverlay && 'aspect-video'
        )}>
          <img 
            src={image} 
            alt={title} 
            className="universal-card-image w-full h-full object-cover" 
          />
        </div>
      )}
      <div className="universal-card-content">
        {category && <span className="universal-card-category">{category}</span>}
        <h3 className="universal-card-title group-hover:text-primary-600 transition-colors">
          {title}
        </h3>
        {description && <p className="universal-card-description">{description}</p>}
        
        <div className="universal-card-footer">
          <button className="universal-card-button">
            View Project
            <svg 
              className="w-4 h-4 transform group-hover:translate-x-1 transition-transform" 
              fill="none" 
              viewBox="0 0 24 24" 
              stroke="currentColor"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};
