import React from 'react';
import { BasketNeo as BasketNeoBase } from './BasketNeo';

export const BasketNeo = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BasketNeoBase, props)
  );
};
