import React from 'react';
import { PurchaseHeaderBar as PurchaseHeaderBarBase } from './PurchaseHeaderBar';

export const PurchaseHeaderBar = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseHeaderBarBase, props)
  );
};
