import React from 'react';
import { DeliveryStepAddress as DeliveryStepAddressBase } from './DeliveryStepAddress';

export const DeliveryStepAddress = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(DeliveryStepAddressBase, props)
  );
};
