import React from 'react';
import { CollectionShowcase as CollectionShowcaseBase } from './CollectionShowcase';

export const CollectionShowcase = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CollectionShowcaseBase, props)
  );
};
