import React from 'react';
import { ItemStoryPanel as ItemStoryPanelBase } from './ItemStoryPanel';

export const ItemStoryPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemStoryPanelBase, props)
  );
};
