import React from 'react';
import { FavoritesList as FavoritesListBase } from './FavoritesList';

export const FavoritesList = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(FavoritesListBase, props)
  );
};
