import React from 'react';
import { ItemPageClassic as ItemPageClassicBase } from './ItemPageClassic';

export const ItemPageClassic = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemPageClassicBase, props)
  );
};
