import React from 'react';
import { ArticleMasonry as ArticleMasonryBase } from './ArticleMasonry';

export const ArticleMasonry = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ArticleMasonryBase, props)
  );
};
