import React from 'react';
import { SharePanel as SharePanelBase } from './SharePanel';

export const SharePanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(SharePanelBase, props)
  );
};
