import React from 'react';
import { HeaderClassic as HeaderClassicBase } from './HeaderClassic';

export const HeaderClassic = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HeaderClassicBase, props)
  );
};
