import React from 'react';
import { ArticleTile as ArticleTileBase } from './ArticleTile';

export const ArticleTile = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ArticleTileBase, props)
  );
};
