import { useState, useEffect, useRef } from 'react';
import clsx from 'clsx';

/**
 * DropZone Component - Versatile file upload component with 3 modern variants
 * @param {Object} props - Component props
 * @param {Object} props.data - DropZone configuration data
 * @param {string} [props.data.title] - Main title
 * @param {string} [props.data.description] - Help text/description
 * @param {string} [props.data.layout='standard'] - Layout variant: 'standard' | 'modern' | 'compact'
 * @param {string} [props.data.theme='light'] - Theme variant: 'light' | 'dark'
 * @param {string} [props.data.accept] - Accepted file types (e.g. 'image/*,application/pdf')
 * @param {boolean} [props.data.multiple=false] - Allow multiple files
 * @param {string} [props.data.icon] - Optional icon override
 * @param {string} [props.data.className] - Additional CSS classes
 * @param {Function} [props.onFilesDrop] - Callback when files are dropped or selected
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const UploadDropArea = ({ data, onFilesDrop, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    data = {};
  }

  const [showLoader, setShowLoader] = useState(loader);
  const [isDragActive, setIsDragActive] = useState(false);
  const [files, setFiles] = useState([]);
  const fileInputRef = useRef(null);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title = 'Upload Files',
    description = 'Drag and drop your files here, or click to browse',
    layout = 'standard',
    theme = 'light',
    accept = '*',
    multiple = false,
    className: dataClassName,
  } = data;

  const isDark = theme === 'dark';

  const handleDragEnter = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragActive(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragActive(false);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragActive(false);

    const droppedFiles = [...e.dataTransfer.files];
    if (droppedFiles.length > 0) {
      const newFiles = multiple ? [...files, ...droppedFiles] : [droppedFiles[0]];
      setFiles(newFiles);
      if (onFilesDrop) onFilesDrop(newFiles);
    }
  };

  const handleFileInputChange = (e) => {
    const selectedFiles = [...e.target.files];
    if (selectedFiles.length > 0) {
      const newFiles = multiple ? [...files, ...selectedFiles] : [selectedFiles[0]];
      setFiles(newFiles);
      if (onFilesDrop) onFilesDrop(newFiles);
    }
  };

  const openFileDialog = () => {
    fileInputRef.current?.click();
  };

  const removeFile = (index) => {
    const newFiles = files.filter((_, i) => i !== index);
    setFiles(newFiles);
    if (onFilesDrop) onFilesDrop(newFiles);
  };

  // --- SKELETON LOADER ---
  if (showLoader) {
    const skeletonClass = isDark ? 'bg-gray-800' : 'bg-gray-100';
    const pulseClass = 'animate-pulse';

    return (
      <div
        id={id}
        className={clsx(
          'w-full rounded-2xl border-2 border-dashed p-12 flex flex-col items-center justify-center',
          isDark ? 'bg-gray-900 border-gray-800' : 'bg-white border-gray-200',
          className,
          dataClassName
        )}
      >
        <div className={clsx('w-16 h-16 rounded-full mb-6', skeletonClass, pulseClass)}></div>
        <div className={clsx('h-6 w-48 rounded mb-4', skeletonClass, pulseClass)}></div>
        <div className={clsx('h-4 w-64 rounded', skeletonClass, pulseClass)}></div>
      </div>
    );
  }

  // --- RENDERING VARIANTS ---

  const renderFilesList = () => {
    if (files.length === 0) return null;

    return (
      <div className="mt-6 w-full space-y-2">
        {files.map((file, index) => (
          <div
            key={`${file.name}-${index}`}
            className={clsx(
              'flex items-center justify-between p-3 rounded-lg border text-sm',
              isDark ? 'bg-gray-800 border-gray-700 text-gray-200' : 'bg-gray-50 border-gray-200 text-gray-700'
            )}
          >
            <div className="flex items-center gap-3 truncate">
              <svg className="w-5 h-5 flex-shrink-0 text-primary-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 21h10a2 2 0 002-2V9.414a1 1 0 00-.293-.707l-5.414-5.414A1 1 0 0012.586 3H7a2 2 0 00-2 2v14a2 2 0 002 2z" />
              </svg>
              <span className="truncate">{file.name}</span>
              <span className={clsx('text-xs', isDark ? 'text-gray-500' : 'text-gray-400')}>
                ({(file.size / 1024).toFixed(1)} KB)
              </span>
            </div>
            <button
              onClick={(e) => {
                e.stopPropagation();
                removeFile(index);
              }}
              className="text-gray-400 hover:text-red-500 transition-colors"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
        ))}
      </div>
    );
  };

  // 1. STANDARD VARIANT
  const renderStandard = () => (
    <div
      id={id}
      className={clsx(
        'relative group cursor-pointer transition-all duration-300',
        'w-full rounded-2xl border-2 border-dashed p-10 sm:p-16',
        isDragActive
          ? 'border-primary-500 bg-primary-50/30'
          : isDark
          ? 'bg-gray-900 border-gray-800 hover:border-gray-700'
          : 'bg-white border-gray-200 hover:border-primary-300 hover:shadow-lg',
        className,
        dataClassName
      )}
      onDragEnter={handleDragEnter}
      onDragOver={handleDragOver}
      onDragLeave={handleDragLeave}
      onDrop={handleDrop}
      onClick={openFileDialog}
    >
      <input
        type="file"
        ref={fileInputRef}
        onChange={handleFileInputChange}
        accept={accept}
        multiple={multiple}
        className="hidden"
      />
      
      <div className="flex flex-col items-center text-center">
        <div className={clsx(
          'w-16 h-16 rounded-2xl flex items-center justify-center mb-6 transition-transform duration-300 group-hover:scale-110',
          isDark ? 'bg-gray-800 text-primary-400' : 'bg-primary-50 text-primary-600'
        )}>
          <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
          </svg>
        </div>
        
        <h3 className={clsx(
          'text-xl font-bold mb-2',
          isDark ? 'text-white' : 'text-gray-900'
        )}>
          {title}
        </h3>
        
        <p className={clsx(
          'text-sm max-w-xs',
          isDark ? 'text-gray-400' : 'text-gray-500'
        )}>
          {description}
        </p>
        
        <div className="mt-8">
          <span className={clsx(
            'inline-flex items-center px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-300',
            isDark
              ? 'bg-primary-600 text-white hover:bg-primary-500'
              : 'bg-gray-900 text-white hover:bg-gray-800'
          )}>
            Browse Files
          </span>
        </div>
      </div>
      
      {renderFilesList()}
    </div>
  );

  // 2. MODERN VARIANT (Glassmorphism + Gradients)
  const renderModern = () => (
    <div
      id={id}
      className={clsx(
        'relative group cursor-pointer transition-all duration-500 overflow-hidden',
        'w-full rounded-3xl p-1',
        isDragActive ? 'scale-[1.02]' : 'hover:scale-[1.01]',
        className,
        dataClassName
      )}
      onDragEnter={handleDragEnter}
      onDragOver={handleDragOver}
      onDragLeave={handleDragLeave}
      onDrop={handleDrop}
      onClick={openFileDialog}
    >
      {/* Animated Background Gradient */}
      <div className={clsx(
        'absolute inset-0 bg-gradient-to-br from-primary-400 via-gray-900 to-black opacity-10 group-hover:opacity-20 transition-opacity duration-500',
        isDragActive && 'opacity-30'
      )} />
      
      {/* Border Gradient */}
      <div className={clsx(
        'absolute inset-0 rounded-3xl bg-gradient-to-r from-primary-500 to-gray-500 opacity-20 group-hover:opacity-50 transition-opacity duration-500',
        isDragActive && 'opacity-100'
      )} />

      <div className={clsx(
        'relative h-full w-full rounded-[22px] p-8 sm:p-12 backdrop-blur-xl flex flex-col items-center text-center',
        isDark ? 'bg-black/80' : 'bg-white/90'
      )}>
        <input
          type="file"
          ref={fileInputRef}
          onChange={handleFileInputChange}
          accept={accept}
          multiple={multiple}
          className="hidden"
        />

        <div className="relative mb-6">
          <div className="absolute inset-0 bg-primary-500 rounded-full blur-2xl opacity-20 animate-pulse" />
          <div className={clsx(
            'relative w-20 h-20 rounded-full flex items-center justify-center border-2 border-primary-500/30',
            isDark ? 'bg-gray-900' : 'bg-primary-50'
          )}>
            <svg className="w-10 h-10 text-primary-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-8l-4-4m0 0L8 8m4-4v12" />
            </svg>
          </div>
        </div>

        <h3 className={clsx(
          'text-2xl font-black tracking-tight mb-2 uppercase',
          isDark ? 'text-white' : 'text-gray-900'
        )}>
          {title}
        </h3>

        <p className={clsx(
          'text-sm font-medium mb-8 max-w-sm leading-relaxed',
          isDark ? 'text-gray-400' : 'text-gray-500'
        )}>
          {description}
        </p>

        <div className="flex items-center gap-4">
          <div className={clsx('h-px w-8', isDark ? 'bg-gray-800' : 'bg-gray-200')} />
          <span className={clsx('text-xs font-bold uppercase tracking-widest', isDark ? 'text-gray-500' : 'text-gray-400')}>
            Or drop here
          </span>
          <div className={clsx('h-px w-8', isDark ? 'bg-gray-800' : 'bg-gray-200')} />
        </div>

        {renderFilesList()}
      </div>
    </div>
  );

  // 3. COMPACT VARIANT
  const renderCompact = () => (
    <div
      id={id}
      className={clsx(
        'group cursor-pointer transition-all duration-300',
        'w-full flex items-center gap-6 p-6 rounded-2xl border-2 border-dashed',
        isDragActive
          ? 'border-primary-500 bg-primary-50/30'
          : isDark
          ? 'bg-gray-900 border-gray-800 hover:border-gray-700'
          : 'bg-white border-gray-200 hover:border-primary-300',
        className,
        dataClassName
      )}
      onDragEnter={handleDragEnter}
      onDragOver={handleDragOver}
      onDragLeave={handleDragLeave}
      onDrop={handleDrop}
      onClick={openFileDialog}
    >
      <input
        type="file"
        ref={fileInputRef}
        onChange={handleFileInputChange}
        accept={accept}
        multiple={multiple}
        className="hidden"
      />

      <div className={clsx(
        'w-12 h-12 rounded-xl flex-shrink-0 flex items-center justify-center transition-transform group-hover:scale-110',
        isDark ? 'bg-gray-800 text-primary-400' : 'bg-primary-50 text-primary-600'
      )}>
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
        </svg>
      </div>

      <div className="flex-grow text-left">
        <h3 className={clsx(
          'text-base font-bold',
          isDark ? 'text-white' : 'text-gray-900'
        )}>
          {title}
        </h3>
        <p className={clsx(
          'text-xs mt-1',
          isDark ? 'text-gray-400' : 'text-gray-500'
        )}>
          {description}
        </p>
      </div>

      <div className="flex-shrink-0">
        <div className={clsx(
          'px-3 py-1.5 rounded-lg text-xs font-bold uppercase transition-all',
          isDark ? 'bg-gray-800 text-gray-300' : 'bg-gray-100 text-gray-600'
        )}>
          Select
        </div>
      </div>
      
      {/* Files list for compact would be different, maybe just a count */}
      {files.length > 0 && (
        <div className="absolute top-0 right-0 -mt-2 -mr-2 bg-primary-600 text-white text-[10px] font-bold w-5 h-5 rounded-full flex items-center justify-center shadow-lg">
          {files.length}
        </div>
      )}
    </div>
  );

  switch (layout) {
    case 'modern':
      return renderModern();
    case 'compact':
      return renderCompact();
    case 'standard':
    default:
      return renderStandard();
  }
};

