import React from 'react';
import { FilterBar as FilterBarBase } from './FilterBar';

export const FilterBar = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(FilterBarBase, props)
  );
};
