import React from 'react';
import { ItemBanner as ItemBannerBase } from './ItemBanner';

export const ItemBanner = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemBannerBase, props)
  );
};
