import React from 'react';
import { LostPageLoadingShell as LostPageLoadingShellBase } from './LostPageLoadingShell';

export const LostPageLoadingShell = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(LostPageLoadingShellBase, props)
  );
};
