import React from 'react';
import { AuthorBadge as AuthorBadgeBase } from './AuthorBadge';

export const AuthorBadge = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(AuthorBadgeBase, props)
  );
};
