import React from 'react';
import { PurchaseCardPanel as PurchaseCardPanelBase } from './PurchaseCardPanel';

export const PurchaseCardPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseCardPanelBase, props)
  );
};
