import React from 'react';
import { ShareBar as ShareBarBase } from './ShareBar';

export const ShareBar = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ShareBarBase, props)
  );
};
