import React from 'react';
import { ItemLoadingShell as ItemLoadingShellBase } from './ItemLoadingShell';

export const ItemLoadingShell = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemLoadingShellBase, props)
  );
};
