import React from 'react';
import { WarrantyStepCover as WarrantyStepCoverBase } from './WarrantyStepCover';

export const WarrantyStepCover = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(WarrantyStepCoverBase, props)
  );
};
