import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const ProgressDots = ({
  data = [],
  currentStep = 0,
  variant = 'standard',
  className,
  loading = false
}) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;

  // Default steps if no data provided
  const defaultSteps = [
    { id: 'contact', title: 'Contact', description: 'Email address' },
    { id: 'shipping', title: 'Shipping', description: 'Delivery info' },
    { id: 'payment', title: 'Payment', description: 'Payment method' },
    { id: 'review', title: 'Review', description: 'Order review' }
  ];

  const steps = data.length > 0 ? data : defaultSteps;

  const CheckIcon = () => (
    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
    </svg>
  );

  const getStepIcon = (stepIndex) => {
    if (stepIndex < currentStep) {
      return <CheckIcon />;
    }
    return <span className="text-sm font-semibold">{stepIndex + 1}</span>;
  };

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <div className={clsx("checkout-progress-indicator", className)}>
        <div className="flex items-center justify-between max-w-4xl mx-auto">
          {Array.from({ length: 4 }).map((_, index) => (
            <div key={index} className="flex items-center">
              <div className="checkout-skeleton w-8 h-8 rounded-full"></div>
              <div className="ml-3">
                <div className="checkout-skeleton h-4 w-16 rounded mb-1"></div>
                <div className="checkout-skeleton h-3 w-20 rounded"></div>
              </div>
              {index < 3 && <div className="checkout-skeleton w-12 h-px mx-4"></div>}
            </div>
          ))}
        </div>
      </div>
    );
  }

  const variants = {
    standard: (
      <div className="flex items-center justify-between max-w-4xl mx-auto">
        {steps.map((step, index) => (
          <div key={step.id} className="flex items-center">
            <div className={clsx(
              "w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium transition-all duration-200",
              index < currentStep
                ? "bg-green-600 text-white"
                : index === currentStep
                  ? "bg-primary-600 text-white"
                  : "bg-gray-200 text-gray-600"
            )}>
              {getStepIcon(index)}
            </div>
            <div className="ml-3">
              <div className={clsx(
                "text-sm font-medium",
                index <= currentStep ? "text-primary-600" : "text-gray-600"
              )}>
                {step.title}
              </div>
              {step.description && (
                <div className="text-xs text-gray-500 mt-0.5">{step.description}</div>
              )}
            </div>
            {index < steps.length - 1 && (
              <div className={clsx(
                "w-12 h-px mx-4 transition-colors duration-200",
                index < currentStep ? "bg-primary-600" : "bg-gray-200"
              )} />
            )}
          </div>
        ))}
      </div>
    ),

    modern: (
      <div className="flex items-center justify-between max-w-5xl mx-auto">
        {steps.map((step, index) => (
          <div key={step.id} className="flex flex-col items-center flex-1">
            <div className="flex items-center w-full">
              <div className={clsx(
                "w-10 h-10 rounded-full flex items-center justify-center text-sm font-semibold transition-all duration-300 shadow-sm",
                index < currentStep
                  ? "bg-green-600 text-white shadow-green-200"
                  : index === currentStep
                    ? "bg-primary-600 text-white shadow-primary-200"
                    : "bg-gray-100 text-gray-600"
              )}>
                {getStepIcon(index)}
              </div>
              {index < steps.length - 1 && (
                <div className="flex-1 h-px mx-4 bg-gray-200">
                  <div className={clsx(
                    "h-full transition-all duration-500 ease-out",
                    index < currentStep ? "bg-primary-600 w-full" : "w-0"
                  )} />
                </div>
              )}
            </div>
            <div className="mt-3 text-center">
              <div className={clsx(
                "text-sm font-semibold transition-colors duration-200",
                index <= currentStep ? "text-primary-600" : "text-gray-600"
              )}>
                {step.title}
              </div>
              {step.description && (
                <div className="text-xs text-gray-500 mt-1">{step.description}</div>
              )}
            </div>
          </div>
        ))}
      </div>
    ),

    minimal: (
      <div className="flex items-center justify-center space-x-8">
        {steps.map((step, index) => (
          <div key={step.id} className="flex items-center space-x-3">
            <div className={clsx(
              "w-6 h-6 rounded-full flex items-center justify-center text-xs font-medium transition-all duration-200",
              index < currentStep
                ? "bg-green-600 text-white"
                : index === currentStep
                  ? "bg-primary-600 text-white"
                  : "bg-gray-300 text-gray-600"
            )}>
              {index < currentStep ? <CheckIcon /> : index + 1}
            </div>
            <span className={clsx(
              "text-sm font-medium transition-colors duration-200",
              index <= currentStep ? "text-primary-600" : "text-gray-600"
            )}>
              {step.title}
            </span>
            {index < steps.length - 1 && (
              <div className="w-8 h-px bg-gray-200" />
            )}
          </div>
        ))}
      </div>
    ),

    card: (
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 max-w-4xl mx-auto">
        {steps.map((step, index) => (
          <div key={step.id} className={clsx(
            "p-4 rounded-lg border-2 transition-all duration-300",
            index < currentStep
              ? "border-green-200 bg-green-50"
              : index === currentStep
                ? "border-primary-300 bg-primary-50 shadow-md"
                : "border-gray-200 bg-white"
          )}>
            <div className="flex items-center space-x-3">
              <div className={clsx(
                "w-8 h-8 rounded-full flex items-center justify-center text-sm font-semibold transition-all duration-200",
                index < currentStep
                  ? "bg-green-600 text-white"
                  : index === currentStep
                    ? "bg-primary-600 text-white"
                    : "bg-gray-200 text-gray-600"
              )}>
                {getStepIcon(index)}
              </div>
              <div>
                <div className={clsx(
                  "text-sm font-semibold",
                  index <= currentStep ? "text-primary-600" : "text-gray-600"
                )}>
                  {step.title}
                </div>
                {step.description && (
                  <div className="text-xs text-gray-500 mt-0.5">{step.description}</div>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>
    ),

    premium: (
      <div className="relative">
        {/* Background gradient */}
        <div className="absolute inset-0 bg-gradient-to-r from-primary-50 via-white to-primary-50 rounded-2xl" />
        <div className="relative bg-white/80 backdrop-blur-sm border border-primary-100 rounded-2xl p-8 shadow-xl">
          <div className="flex items-center justify-between max-w-5xl mx-auto">
            {steps.map((step, index) => (
              <div key={step.id} className="flex flex-col items-center flex-1">
                <div className="relative">
                  <div className={clsx(
                    "w-12 h-12 rounded-full flex items-center justify-center text-sm font-bold transition-all duration-300 shadow-lg",
                    index < currentStep
                      ? "bg-gradient-to-br from-green-500 to-green-600 text-white shadow-green-300"
                      : index === currentStep
                        ? "bg-gradient-to-br from-primary-600 to-primary-700 text-white shadow-primary-300"
                        : "bg-gradient-to-br from-gray-100 to-gray-200 text-gray-600"
                  )}>
                    {getStepIcon(index)}
                  </div>
                  {index === currentStep && (
                    <div className="absolute -inset-1 bg-primary-400 rounded-full animate-pulse opacity-20" />
                  )}
                </div>
                <div className="mt-4 text-center">
                  <div className={clsx(
                    "text-sm font-bold tracking-wide transition-colors duration-200",
                    index <= currentStep ? "text-primary-700" : "text-gray-600"
                  )}>
                    {step.title}
                  </div>
                  {step.description && (
                    <div className="text-xs text-gray-500 mt-1 font-medium">{step.description}</div>
                  )}
                </div>
                {index < steps.length - 1 && (
                  <div className="flex-1 w-px bg-gradient-to-b from-primary-200 to-transparent mx-auto mt-4 h-8" />
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    )
  };

  return (
    <div className={clsx("checkout-progress-indicator", className)}>
      {variants[variant] || variants.standard}
    </div>
  );
};
