import React from 'react';
import clsx from 'clsx';
import '../../styles/contact.css';

/**
 * ReachUsLoadingShell Component - Skeleton loader for contact components
 * @param {Object} props - Component props
 * @param {Object} props.data - Skeleton configuration data
 * @param {string} [props.data.variant='modern'] - Style variant: 'modern' | 'minimal' | 'classic' | 'split' | 'card'
 * @param {string} [props.className] - Additional CSS classes
 */
export const ReachUsLoadingShell = ({
  data = {},
  className
}) => {
  const {
    variant = 'modern',
  } = data;

  // Container classes
  const containerClasses = {
    modern: 'contact-modern py-20 bg-white',
    minimal: 'contact-minimal py-20 bg-neutral-50',
    classic: 'contact-classic py-24 bg-white',
    split: 'contact-split min-h-screen bg-white',
    card: 'contact-card py-20 bg-neutral-50',
  };

  const renderSkeleton = () => {
    switch (variant) {
      case 'modern':
        return (
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="text-center mb-16">
              <div className="contact-skeleton h-10 w-64 rounded-lg mx-auto mb-4"></div>
              <div className="contact-skeleton h-5 w-96 rounded mx-auto"></div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {[1, 2, 3].map((i) => (
                <div key={i} className="bg-neutral-50 p-8 rounded-2xl">
                  <div className="contact-skeleton h-12 w-12 rounded-xl mb-6"></div>
                  <div className="contact-skeleton h-6 w-32 rounded mb-2"></div>
                  <div className="contact-skeleton h-4 w-48 rounded"></div>
                </div>
              ))}
            </div>
          </div>
        );
      case 'minimal':
        return (
          <div className="max-w-4xl mx-auto px-4">
            <div className="contact-skeleton h-10 w-48 rounded mb-12"></div>
            <div className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="contact-skeleton h-14 w-full rounded-xl"></div>
                <div className="contact-skeleton h-14 w-full rounded-xl"></div>
              </div>
              <div className="contact-skeleton h-14 w-full rounded-xl"></div>
              <div className="contact-skeleton h-32 w-full rounded-xl"></div>
              <div className="contact-skeleton h-14 w-40 rounded-xl"></div>
            </div>
          </div>
        );
      case 'classic':
        return (
          <div className="max-w-7xl mx-auto px-4 grid grid-cols-1 lg:grid-cols-2 gap-16">
            <div>
              <div className="contact-skeleton h-12 w-80 rounded mb-6"></div>
              <div className="contact-skeleton h-5 w-full rounded mb-2"></div>
              <div className="contact-skeleton h-5 w-full rounded mb-10"></div>
              <div className="space-y-6">
                {[1, 2, 3].map((i) => (
                  <div key={i} className="flex items-center space-x-4">
                    <div className="contact-skeleton h-6 w-6 rounded-full"></div>
                    <div className="contact-skeleton h-5 w-48 rounded"></div>
                  </div>
                ))}
              </div>
            </div>
            <div className="bg-white border border-neutral-200 p-8 rounded-lg shadow-sm">
              <div className="space-y-4">
                <div className="contact-skeleton h-12 w-full rounded"></div>
                <div className="contact-skeleton h-12 w-full rounded"></div>
                <div className="contact-skeleton h-12 w-full rounded"></div>
                <div className="contact-skeleton h-32 w-full rounded"></div>
                <div className="contact-skeleton h-12 w-full rounded"></div>
              </div>
            </div>
          </div>
        );
      case 'split':
        return (
          <div className="flex flex-col lg:flex-row min-h-screen">
            <div className="lg:w-1/2 bg-neutral-900 p-12 flex flex-col justify-center">
              <div className="max-w-md mx-auto w-full">
                <div className="contact-skeleton bg-neutral-800 h-10 w-64 rounded mb-6"></div>
                <div className="contact-skeleton bg-neutral-800 h-4 w-full rounded mb-2"></div>
                <div className="contact-skeleton bg-neutral-800 h-4 w-2/3 rounded mb-12"></div>
                <div className="space-y-8">
                  {[1, 2].map((i) => (
                    <div key={i}>
                      <div className="contact-skeleton bg-neutral-800 h-5 w-32 rounded mb-2"></div>
                      <div className="contact-skeleton bg-neutral-800 h-4 w-48 rounded"></div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
            <div className="lg:w-1/2 p-12 flex items-center justify-center">
              <div className="max-w-md w-full space-y-6">
                <div className="contact-skeleton h-14 w-full rounded-lg"></div>
                <div className="contact-skeleton h-14 w-full rounded-lg"></div>
                <div className="contact-skeleton h-32 w-full rounded-lg"></div>
                <div className="contact-skeleton h-14 w-full rounded-lg"></div>
              </div>
            </div>
          </div>
        );
      case 'card':
        return (
          <div className="max-w-5xl mx-auto px-4">
            <div className="bg-white rounded-3xl shadow-xl overflow-hidden grid grid-cols-1 md:grid-cols-2">
              <div className="p-12 bg-black">
                <div className="contact-skeleton bg-neutral-800 h-8 w-40 rounded mb-8"></div>
                <div className="space-y-10">
                  {[1, 2, 3].map((i) => (
                    <div key={i} className="flex items-center space-x-4">
                      <div className="contact-skeleton bg-neutral-800 h-10 w-10 rounded-full"></div>
                      <div className="contact-skeleton bg-neutral-800 h-5 w-32 rounded"></div>
                    </div>
                  ))}
                </div>
              </div>
              <div className="p-12">
                <div className="space-y-6">
                  <div className="contact-skeleton h-12 w-full rounded-xl"></div>
                  <div className="contact-skeleton h-12 w-full rounded-xl"></div>
                  <div className="contact-skeleton h-32 w-full rounded-xl"></div>
                  <div className="contact-skeleton h-14 w-full rounded-xl"></div>
                </div>
              </div>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className={clsx(
      containerClasses[variant] || containerClasses.modern,
      className
    )}>
      {renderSkeleton()}
    </div>
  );
};

