import React from 'react';
import { ValueHighlights as ValueHighlightsBase } from './ValueHighlights';

export const ValueHighlights = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ValueHighlightsBase, props)
  );
};
