import React from 'react';
import { PurchaseActions as PurchaseActionsBase } from './PurchaseActions';

export const PurchaseActions = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseActionsBase, props)
  );
};
