import { useId } from 'react';
import clsx from 'clsx';

/**
 * DetailsContent - Fully reusable details section for any page (About, Service, Feature, Project, Blog details, etc.)
 *
 * Supports both flat props and a single `data` object (CMS/JSON friendly).
 *
 * @param {Object} props
 * @param {Object} [props.data] - Optional data object (useful when wiring CMS/JSON)
 * @param {string} [props.title] - Heading text
 * @param {string} [props.label] - Small label/badge text (optional)
 * @param {string} [props.subtitle] - Subtitle text (optional)
 * @param {string|string[]|import('react').ReactNode} [props.description] - Description text or rich content
 * @param {string} [props.descriptionHtml] - Description HTML string (optional; CMS-provided HTML)
 * @param {string|{src:string,alt?:string,caption?:string,width?:number,height?:number,sizes?:string,loading?:'lazy'|'eager',decoding?:'async'|'sync'|'auto',className?:string,style?:Object}} [props.image] - Image src or image config (optional)
 * @param {{text:string,href:string,target?:string,rel?:string}} [props.button] - Optional button link
 * @param {'left'|'right'} [props.imagePosition='right'] - Desktop layout: image left/text right or vice-versa
 * @param {'light'|'dark'} [props.theme='light'] - Color theme
 * @param {'h1'|'h2'|'h3'|'h4'|'h5'|'h6'} [props.titleAs='h2'] - Heading element to render
 * @param {string} [props.id] - Optional section id
 * @param {string} [props.className] - Optional extra classes for the root section
 * @param {string} [props.textClassName] - Optional extra classes for the text column
 * @param {string} [props.imageClassName] - Optional extra classes for the image column
 */
export const DetailsContent = ({
  data,
  title: titleProp,
  label: labelProp,
  subtitle: subtitleProp,
  description: descriptionProp,
  descriptionHtml: descriptionHtmlProp,
  image: imageProp,
  button: buttonProp,
  imagePosition: imagePositionProp,
  theme: themeProp,
  titleAs: titleAsProp,
  id,
  className,
  textClassName,
  imageClassName,
}) => {
  const reactId = useId();

  const resolved = {
    ...(data && typeof data === 'object' ? data : {}),
  };

  if (titleProp !== undefined) resolved.title = titleProp;
  if (labelProp !== undefined) resolved.label = labelProp;
  if (subtitleProp !== undefined) resolved.subtitle = subtitleProp;
  if (descriptionProp !== undefined) resolved.description = descriptionProp;
  if (descriptionHtmlProp !== undefined) resolved.descriptionHtml = descriptionHtmlProp;
  if (imageProp !== undefined) resolved.image = imageProp;
  if (buttonProp !== undefined) resolved.button = buttonProp;
  if (imagePositionProp !== undefined) resolved.imagePosition = imagePositionProp;
  if (themeProp !== undefined) resolved.theme = themeProp;
  if (titleAsProp !== undefined) resolved.titleAs = titleAsProp;

  const title = resolved.title;
  const label = resolved.label;
  const subtitle = resolved.subtitle;
  const description = resolved.description;
  const descriptionHtml = resolved.descriptionHtml;
  const image = resolved.image;
  const button = resolved.button;
  const imagePosition =
    resolved.imagePosition === 'left' || resolved.imagePosition === 'right'
      ? resolved.imagePosition
      : 'right';
  const theme = resolved.theme === 'dark' ? 'dark' : 'light';
  const isDark = theme === 'dark';
  const TitleTag = resolved.titleAs || 'h2';

  const hasTextHeader = Boolean(label || subtitle || title);
  const hasDescription = Boolean(description || descriptionHtml);
  const hasButton = Boolean(button?.text && button?.href);
  const hasText = hasTextHeader || hasDescription || hasButton;
  const hasImage = Boolean(image);

  if (!hasText && !hasImage) return null;

  const titleId =
    typeof title === 'string' && title.trim().length > 0
      ? `${id || `details-content-${reactId}`}-title`
      : undefined;

  const imageCfg =
    typeof image === 'string'
      ? { src: image, alt: typeof title === 'string' ? title : 'Details image' }
      : image || null;

  const renderDescription = () => {
    if (descriptionHtml) {
      return (
        <div
          className={clsx(
            'text-sm sm:text-base leading-relaxed',
            isDark ? 'text-slate-300' : 'text-slate-600',
            isDark ? '[&_a]:text-primary-300' : '[&_a]:text-primary-600',
            '[&_a]:underline-offset-4 hover:[&_a]:underline',
            isDark ? '[&_strong]:text-white' : '[&_strong]:text-slate-900',
            '[&_ul]:list-disc [&_ul]:pl-5 [&_ol]:list-decimal [&_ol]:pl-5 [&_li]:my-1',
            isDark ? '[&_code]:text-slate-200' : '[&_code]:text-slate-800'
          )}
          dangerouslySetInnerHTML={{ __html: descriptionHtml }}
        />
      );
    }

    if (!description) return null;

    if (Array.isArray(description)) {
      return (
        <div
          className={clsx(
            'space-y-3 text-sm sm:text-base leading-relaxed',
            isDark ? 'text-slate-300' : 'text-slate-600'
          )}
        >
          {description.filter(Boolean).map((line, idx) => (
            <p key={idx} className="whitespace-pre-line">
              {line}
            </p>
          ))}
        </div>
      );
    }

    if (typeof description === 'string') {
      return (
        <p
          className={clsx(
            'text-sm sm:text-base leading-relaxed whitespace-pre-line',
            isDark ? 'text-slate-300' : 'text-slate-600'
          )}
        >
          {description}
        </p>
      );
    }

    return (
      <div
        className={clsx(
          'text-sm sm:text-base leading-relaxed',
          isDark ? 'text-slate-300' : 'text-slate-600'
        )}
      >
        {description}
      </div>
    );
  };

  return (
    <section
      id={id}
      aria-labelledby={titleId}
      className={clsx(
        'w-full',
        'py-10 sm:py-12 lg:py-16',
        isDark
          ? 'bg-gradient-to-b from-slate-950 via-slate-950 to-slate-900'
          : 'bg-gradient-to-b from-white via-white to-slate-50',
        className
      )}
    >
      <div
        className={clsx(
          'mx-auto w-full max-w-7xl',
          'px-4 sm:px-6 lg:px-8'
        )}
      >
        <div
          className={clsx(
            'grid grid-cols-1',
            hasImage ? 'lg:grid-cols-2' : 'lg:grid-cols-1',
            'gap-8 lg:gap-12',
            'items-start lg:items-center'
          )}
        >
          {/* Text */}
          {hasText && (
            <article
              className={clsx(
                'min-w-0',
                !hasImage ? 'w-full max-w-none' : 'w-full',
                imagePosition === 'left' ? 'order-2' : 'order-1',
                textClassName
              )}
            >
              {hasTextHeader && (
                <header className="space-y-3">
                  {label && (
                    <p
                      className={clsx(
                        'inline-flex w-fit items-center rounded-full px-3 py-1 text-xs sm:text-sm font-semibold ring-1 ring-inset',
                        isDark
                          ? 'bg-primary-500/15 text-primary-300 ring-primary-400/20'
                          : 'bg-primary-50 text-primary-700 ring-primary-100'
                      )}
                    >
                      {label}
                    </p>
                  )}

                  {title && (
                    <TitleTag
                      id={titleId}
                      className={clsx(
                        'text-2xl sm:text-3xl lg:text-4xl font-bold tracking-tight',
                        isDark ? 'text-white' : 'text-slate-900'
                      )}
                    >
                      {title}
                    </TitleTag>
                  )}

                  {subtitle && (
                    <p
                      className={clsx(
                        'text-base sm:text-lg leading-relaxed',
                        isDark ? 'text-slate-200' : 'text-slate-700'
                      )}
                    >
                      {subtitle}
                    </p>
                  )}
                </header>
              )}

              {(hasDescription || hasButton) && (
                <div className={clsx('mt-5 sm:mt-6', hasButton ? 'space-y-6' : undefined)}>
                  {hasDescription && renderDescription()}

                  {hasButton && (
                    <a
                      href={button.href}
                      target={button.target}
                      rel={button.rel || (button.target === '_blank' ? 'noreferrer noopener' : undefined)}
                      className={clsx(
                        'inline-flex w-fit items-center justify-center rounded-xl px-5 py-3 text-sm sm:text-base font-semibold text-white shadow-sm transition-colors focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2',
                        isDark
                          ? 'bg-primary-500 hover:bg-primary-400 focus-visible:ring-primary-300 focus-visible:ring-offset-slate-950'
                          : 'bg-primary-600 hover:bg-primary-700 focus-visible:ring-primary-500 focus-visible:ring-offset-white'
                      )}
                    >
                      {button.text}
                    </a>
                  )}
                </div>
              )}
            </article>
          )}

          {/* Image */}
          {hasImage && imageCfg?.src && (
            <figure
              className={clsx(
                'min-w-0',
                imagePosition === 'left' ? 'order-1' : 'order-2',
                imageClassName
              )}
            >
              <img
                src={imageCfg.src}
                alt={imageCfg.alt || ''}
                width={imageCfg.width}
                height={imageCfg.height}
                sizes={imageCfg.sizes || '(min-width: 1024px) 50vw, 100vw'}
                loading={imageCfg.loading || 'lazy'}
                decoding={imageCfg.decoding || 'async'}
                style={imageCfg.style}
                className={clsx(
                  'w-full h-auto',
                  'rounded-2xl',
                  isDark ? 'bg-slate-900' : 'bg-slate-100',
                  'shadow-sm ring-1 ring-inset',
                  isDark ? 'ring-slate-800' : 'ring-slate-200',
                  typeof imageCfg.className === 'string' ? imageCfg.className : undefined
                )}
              />
              {imageCfg?.caption && (
                <figcaption
                  className={clsx(
                    'mt-3 text-xs sm:text-sm',
                    isDark ? 'text-slate-400' : 'text-slate-500'
                  )}
                >
                  {imageCfg.caption}
                </figcaption>
              )}
            </figure>
          )}
        </div>
      </div>
    </section>
  );
};


