import React from 'react';
import { RatingWall as RatingWallBase } from './RatingWall';

export const RatingWall = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(RatingWallBase, props)
  );
};
