import React from 'react';
import { PlansElite as PlansEliteBase } from './PlansElite';

export const PlansElite = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PlansEliteBase, props)
  );
};
