import React from 'react';
import { ItemShowcaseHeritage as ItemShowcaseHeritageBase } from './ItemShowcaseHeritage';

export const ItemShowcaseHeritage = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemShowcaseHeritageBase, props)
  );
};
