import React from 'react';
import { HeaderElite as HeaderEliteBase } from './HeaderElite';

export const HeaderElite = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HeaderEliteBase, props)
  );
};
