import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { ReachUsLoadingShell } from '../ReachUsLoadingShell';
import '../../styles/contact.css';

/**
 * Contact Component - A versatile contact section with 7 variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Contact section data
 * @param {string} [props.data.variant='modern'] - Style variant: 'modern' | 'minimal' | 'classic' | 'split' | 'card' | 'map' | 'mapFull'
 * @param {string} [props.id] - Optional section id (falls back to data.id)
 * @param {boolean} [props.loading=false] - Loading state for skeleton
 * @param {string} [props.className] - Additional CSS classes
 */
export const ReachUsPanel = ({
  data = {},
  className,
  loading = false,
  id
}) => {
  const [timedOut, setTimedOut] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });

  const variant = data.variant || 'modern';

  // Reset timeout when data changes to ensure dynamic content works
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setTimedOut(false);
    }
  }, [loading, data]); // Added data to dependencies to reset on data changes

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
    // Add logic here
  };

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <ReachUsLoadingShell data={{ variant }} className={className} />;
  }

  const renderVariant = () => {
    switch (variant) {
      case 'modern':
        return (
          <div className="max-w-7xl mx-auto px-4 pt-8 sm:px-6 lg:px-8">
            {(data.title || data.subtitle || data.description) && (
              <div className="text-center mb-16">
                {data.title && (
                  <h2 className="text-4xl font-black text-black mb-4 uppercase tracking-tighter">
                    {data.title}
                  </h2>
                )}
                {(data.subtitle || data.description) && (
                  <p className="text-gray-500 max-w-2xl mx-auto text-lg">
                    {data.subtitle || data.description}
                  </p>
                )}
              </div>
            )}
            {data.info && data.info.length > 0 && (
              <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-16">
                {data.info.map((item, idx) => (
                  <div key={idx} className="bg-neutral-50 p-8 rounded-3xl border border-transparent hover:border-primary-600 transition-all group">
                    {item.icon && <div className="text-3xl mb-6 group-hover:scale-110 transition-transform inline-block">{item.icon}</div>}
                    {item.title && <h3 className="text-xl font-bold text-black mb-2">{item.title}</h3>}
                    {item.value && <p className="text-gray-600 group-hover:text-primary-600 transition-colors">{item.value}</p>}
                  </div>
                ))}
              </div>
            )}
          </div>
        );

      case 'minimal':
        return (
          <div className="max-w-4xl mx-auto px-4 py-20">
            {data.title && (
              <h2 className="text-5xl font-black text-black mb-12 tracking-tight">
                {data.title}
              </h2>
            )}
            {data.form && (
              <form onSubmit={handleSubmit} className="space-y-6">
                {(data.form.fields || [
                  { name: 'name', type: 'text', placeholder: data.form?.placeholders?.name, required: true, colSpan: 'col-span-6' },
                  { name: 'email', type: 'email', placeholder: data.form?.placeholders?.email, required: true, colSpan: 'col-span-6' },
                  { name: 'subject', type: 'text', placeholder: data.form?.placeholders?.subject, colSpan: 'col-span-12' },
                  { name: 'message', type: 'textarea', placeholder: data.form?.placeholders?.message, required: true, colSpan: 'col-span-12', rows: 4 }
                ]).map((field, idx) => (
                  <div key={field.name} className={field.colSpan ? `col-span-${field.colSpan.split('-')[2]}` : ''}>
                    {field.type === 'textarea' ? (
                      <textarea
                        name={field.name}
                        placeholder={field.placeholder}
                        rows={field.rows || 3}
                        required={field.required}
                        className="w-full bg-white border-2 border-neutral-200 rounded-2xl px-6 py-4 focus:border-black outline-none transition-colors resize-none"
                        onChange={handleInputChange}
                      />
                    ) : (
                      <input
                        type={field.type || 'text'}
                        name={field.name}
                        placeholder={field.placeholder}
                        required={field.required}
                        className="w-full bg-white border-2 border-neutral-200 rounded-2xl px-6 py-4 focus:border-black outline-none transition-colors"
                        onChange={handleInputChange}
                      />
                    )}
                  </div>
                ))}
                {data.form?.buttonText && (
                  <button
                    type="submit"
                    className="bg-black text-white px-10 py-4 rounded-2xl font-bold hover:bg-gray-800 transition-colors"
                  >
                    {data.form.buttonText}
                  </button>
                )}
              </form>
            )}
          </div>
        );

      case 'classic':
        return (
          <div className="max-w-7xl mx-auto px-4 grid grid-cols-1 lg:grid-cols-2 gap-20 py-24">
            <div>
              {data.title && (
                <h2 className="text-4xl font-bold text-black mb-6">
                  {data.title}
                </h2>
              )}
              {(data.subtitle || data.description) && (
                <p className="text-gray-600 text-lg mb-12">
                  {data.subtitle || data.description}
                </p>
              )}
              {data.info && data.info.length > 0 && (
                <div className="space-y-8">
                  {data.info.map((item, idx) => (
                    <div key={idx} className="flex flex-col">
                      {item.label && <span className="text-xs uppercase tracking-widest text-gray-400 font-bold mb-1">{item.label}</span>}
                      {item.value && <span className="text-xl text-black font-medium">{item.value}</span>}
                    </div>
                  ))}
                </div>
              )}
            </div>
            {data.form && (
              <div className="bg-white border border-neutral-100 p-10 rounded-2xl shadow-2xl shadow-neutral-200/50">
                <form onSubmit={handleSubmit} className="space-y-6">
                  {(data.form.fields || [
                    { name: 'name', type: 'text', label: data.form?.labels?.name || 'Name' },
                    { name: 'email', type: 'email', label: data.form?.labels?.email || 'Email' },
                    { name: 'message', type: 'textarea', label: data.form?.labels?.message || 'Message', rows: 3 }
                  ]).map((field, idx) => (
                    <div key={field.name} className="space-y-2">
                      <label className="text-sm font-bold text-black">{field.label}</label>
                      {field.type === 'textarea' ? (
                        <textarea
                          name={field.name}
                          rows={field.rows || 3}
                          className="w-full border-b-2 border-neutral-100 focus:border-black py-2 outline-none transition-colors resize-none"
                          onChange={handleInputChange}
                        />
                      ) : (
                        <input
                          type={field.type || 'text'}
                          name={field.name}
                          className="w-full border-b-2 border-neutral-100 focus:border-black py-2 outline-none transition-colors"
                          onChange={handleInputChange}
                        />
                      )}
                    </div>
                  ))}
                  {data.form?.buttonText && (
                    <button type="submit" className="w-full bg-black text-white py-4 rounded-lg font-bold tracking-wide hover:shadow-lg transition-all">
                      {data.form.buttonText}
                    </button>
                  )}
                </form>
              </div>
            )}
          </div>
        );

      case 'split':
        return (
          <div className="flex flex-col lg:flex-row min-h-screen">
            <div className="lg:w-1/2 bg-black text-white p-12 md:p-24 flex flex-col justify-center">
              <div className="max-w-md mx-auto w-full">
                {data.title && (
                  <h2 className="text-5xl font-black mb-8 leading-tight">
                    {data.title}
                  </h2>
                )}
                {(data.subtitle || data.description) && (
                  <p className="text-gray-400 text-xl mb-12">
                    {data.subtitle || data.description}
                  </p>
                )}
                {data.info && data.info.length > 0 && (
                  <div className="space-y-12">
                    {data.info.map((item, idx) => (
                      <div key={idx}>
                        {item.label && <h3 className="text-gray-500 font-bold uppercase tracking-widest text-xs mb-4">{item.label}</h3>}
                        {item.value && (
                          item.isLink ? (
                            <p className="text-lg underline underline-offset-8">{item.value}</p>
                          ) : (
                            <p className="text-lg whitespace-pre-line">{item.value}</p>
                          )
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
            <div className="lg:w-1/2 p-12 md:p-24 flex items-center justify-center bg-white">
              {data.form && (
                <form onSubmit={handleSubmit} className="max-w-md w-full space-y-8">
                  {(data.form.fields || [
                    { name: 'name', type: 'text', placeholder: data.form?.placeholders?.name },
                    { name: 'email', type: 'email', placeholder: data.form?.placeholders?.email },
                    { name: 'message', type: 'textarea', placeholder: data.form?.placeholders?.message, rows: 4 }
                  ]).map((field, idx) => (
                    field.type === 'textarea' ? (
                      <textarea
                        key={field.name}
                        name={field.name}
                        placeholder={field.placeholder}
                        rows={field.rows || 4}
                        className="w-full border-b-2 border-neutral-200 focus:border-black py-4 text-lg outline-none transition-all resize-none"
                        onChange={handleInputChange}
                      />
                    ) : (
                      <input
                        key={field.name}
                        type={field.type || 'text'}
                        name={field.name}
                        placeholder={field.placeholder}
                        className="w-full border-b-2 border-neutral-200 focus:border-black py-4 text-lg outline-none transition-all"
                        onChange={handleInputChange}
                      />
                    )
                  ))}
                  {data.form?.buttonText && (
                    <button type="submit" className="group flex items-center space-x-4 text-xl font-bold text-black hover:translate-x-2 transition-transform">
                      <span>{data.form.buttonText}</span>
                      <span className="text-2xl"></span>
                    </button>
                  )}
                </form>
              )}
            </div>
          </div>
        );

      case 'card':
        return (
          <div className="max-w-6xl mx-auto px-4 py-20">
            <div className="bg-white rounded-[40px] shadow-2xl shadow-neutral-200 overflow-hidden grid grid-cols-1 lg:grid-cols-5 min-h-[600px]">
              <div className="lg:col-span-2 bg-black p-12 flex flex-col justify-between text-white">
                {(data.title || data.subtitle || data.description) && (
                  <div>
                    {data.title && <h2 className="text-3xl font-bold mb-4">{data.title}</h2>}
                    {(data.subtitle || data.description) && (
                      <p className="text-gray-400">
                        {data.subtitle || data.description}
                      </p>
                    )}
                  </div>
                )}
                {data.info && data.info.length > 0 && (
                  <div className="space-y-8">
                    {data.info.map((item, idx) => (
                      <div key={idx} className="flex items-center space-x-6 group cursor-pointer">
                        {item.icon && (
                          <div className="w-12 h-12 rounded-full border border-neutral-800 flex items-center justify-center group-hover:bg-white group-hover:text-black transition-all">
                            {item.icon}
                          </div>
                        )}
                        {item.value && <span className="text-lg">{item.value}</span>}
                      </div>
                    ))}
                  </div>
                )}
                {data.socials && data.socials.length > 0 && (
                  <div className="flex space-x-4">
                    {data.socials.map((social, idx) => (
                      <a
                        key={idx}
                        href={social.link}
                        className="w-10 h-10 rounded-lg bg-neutral-900 flex items-center justify-center hover:bg-neutral-800 cursor-pointer transition-colors"
                      >
                        {social.name}
                      </a>
                    ))}
                  </div>
                )}
              </div>
              {data.form && (
                <div className="lg:col-span-3 p-12 bg-white">
                  <form onSubmit={handleSubmit} className="space-y-8">
                    {(data.form.fields || [
                      { name: 'firstName', type: 'text', label: data.form?.labels?.firstName || 'First Name', colSpan: 'col-span-6' },
                      { name: 'lastName', type: 'text', label: data.form?.labels?.lastName || 'Last Name', colSpan: 'col-span-6' },
                      { name: 'email', type: 'email', label: data.form?.labels?.email || 'Email', colSpan: 'col-span-12' },
                      { name: 'message', type: 'textarea', label: data.form?.labels?.message || 'Message', colSpan: 'col-span-12', rows: 4 }
                    ]).map((field, idx) => (
                      <div key={field.name} className={`space-y-2 ${field.colSpan ? field.colSpan : ''}`}>
                        <label className="text-xs font-black uppercase text-gray-400">{field.label}</label>
                        {field.type === 'textarea' ? (
                          <textarea
                            name={field.name}
                            rows={field.rows || 4}
                            className="w-full bg-neutral-50 border-none rounded-xl px-6 py-4 focus:ring-2 focus:ring-black outline-none transition-all resize-none"
                            onChange={handleInputChange}
                          />
                        ) : (
                          <input
                            type={field.type || 'text'}
                            name={field.name}
                            className="w-full bg-neutral-50 border-none rounded-xl px-6 py-4 focus:ring-2 focus:ring-black outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        )}
                      </div>
                    ))}
                    {data.form?.buttonText && (
                      <button type="submit" className="bg-black text-white px-12 py-5 rounded-2xl font-black shadow-xl shadow-neutral-200 hover:scale-[1.02] active:scale-95 transition-all">
                        {data.form.buttonText}
                      </button>
                    )}
                  </form>
                </div>
              )}
            </div>
          </div>
        );

      case 'map':
        return (
          <div className="max-w-7xl mx-auto px-4 py-16 grid grid-cols-1 lg:grid-cols-2 gap-12 items-stretch">
            <div className="space-y-10">
              {(data.title || data.subtitle || data.description) && (
                <div className="space-y-4">
                  {data.title && <h2 className="text-4xl font-black text-black tracking-tight">{data.title}</h2>}
                  {(data.subtitle || data.description) && (
                    <p className="text-gray-600 text-lg">{data.subtitle || data.description}</p>
                  )}
                </div>
              )}
              {data.info && data.info.length > 0 && (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {data.info.map((item, idx) => (
                    <div key={idx} className="p-5 rounded-2xl bg-neutral-50 border border-neutral-100 shadow-sm">
                      {item.label && <p className="text-xs uppercase tracking-widest text-gray-400 font-bold mb-1">{item.label}</p>}
                      {item.value && (
                        <p className="text-lg text-black whitespace-pre-line">
                          {item.value}
                        </p>
                      )}
                    </div>
                  ))}
                </div>
              )}
              {data.form && (
                <form onSubmit={handleSubmit} className="space-y-6">
                  {(data.form.fields || [
                    { name: 'name', type: 'text', label: data.form?.labels?.name || 'Name' },
                    { name: 'email', type: 'email', label: data.form?.labels?.email || 'Email' },
                    { name: 'message', type: 'textarea', label: data.form?.labels?.message || 'Message', rows: 4 }
                  ]).map((field) => (
                    <div key={field.name} className="space-y-2">
                      {field.label && <label className="text-sm font-bold text-black">{field.label}</label>}
                      {field.type === 'textarea' ? (
                        <textarea
                          name={field.name}
                          rows={field.rows || 4}
                          placeholder={field.placeholder}
                          className="w-full bg-white border-2 border-neutral-200 rounded-2xl px-4 py-3 focus:border-black outline-none transition-colors resize-none"
                          onChange={handleInputChange}
                        />
                      ) : (
                        <input
                          type={field.type || 'text'}
                          name={field.name}
                          placeholder={field.placeholder}
                          className="w-full bg-white border-2 border-neutral-200 rounded-2xl px-4 py-3 focus:border-black outline-none transition-colors"
                          onChange={handleInputChange}
                        />
                      )}
                    </div>
                  ))}
                  {data.form?.buttonText && (
                    <button
                      type="submit"
                      className="inline-flex items-center justify-center bg-black text-white px-8 py-3 rounded-xl font-bold hover:bg-gray-800 transition-colors"
                    >
                      {data.form.buttonText}
                    </button>
                  )}
                </form>
              )}
            </div>
            <div className="bg-neutral-100 rounded-3xl overflow-hidden shadow-xl h-[400px] lg:h-full">
              {data.mapEmbedUrl ? (
                <iframe
                  src={data.mapEmbedUrl}
                  title={data.mapTitle || 'Location Map'}
                  className="w-full h-full border-0"
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                  allowFullScreen
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center text-gray-500 text-center px-6">
                  Add a `mapEmbedUrl` to show a map here.
                </div>
              )}
            </div>
          </div>
        );

      case 'mapFull':
        return (
          <div className="space-y-10">
            <div className="max-w-6xl mx-auto px-4 pt-8 space-y-10">
              {(data.title || data.subtitle || data.description) && (
                <div className="space-y-4 text-center">
                  {data.title && <h2 className="text-4xl md:text-5xl font-black text-black tracking-tight">{data.title}</h2>}
                  {(data.subtitle || data.description) && (
                    <p className="text-gray-600 text-lg max-w-3xl mx-auto">
                      {data.subtitle || data.description}
                    </p>
                  )}
                </div>
              )}
              {data.info && data.info.length > 0 && (
                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                  {data.info.map((item, idx) => (
                    <div key={idx} className="p-6 rounded-2xl bg-neutral-50 border border-neutral-100 shadow-sm text-center">
                      {item.label && <p className="text-xs uppercase tracking-widest text-gray-400 font-bold mb-2">{item.label}</p>}
                      {item.value && (
                        <p className="text-lg text-black whitespace-pre-line">
                          {item.value}
                        </p>
                      )}
                    </div>
                  ))}
                </div>
              )}
              {data.form && (
                <div className="bg-white border border-neutral-100 rounded-3xl shadow-xl shadow-neutral-200/60 p-8 md:p-10">
                  <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    {(data.form.fields || [
                      { name: 'name', type: 'text', label: data.form?.labels?.name || 'Name' },
                      { name: 'email', type: 'email', label: data.form?.labels?.email || 'Email' },
                      { name: 'subject', type: 'text', label: data.form?.labels?.subject || 'Subject', colSpan: 'col-span-2' },
                      { name: 'message', type: 'textarea', label: data.form?.labels?.message || 'Message', rows: 4, colSpan: 'col-span-2' }
                    ]).map((field) => (
                      <div key={field.name} className={field.colSpan || ''}>
                        {field.label && <label className="text-sm font-bold text-black block mb-2">{field.label}</label>}
                        {field.type === 'textarea' ? (
                          <textarea
                            name={field.name}
                            rows={field.rows || 4}
                            placeholder={field.placeholder}
                            className="w-full bg-white border-2 border-neutral-200 rounded-2xl px-4 py-3 focus:border-black outline-none transition-colors resize-none"
                            onChange={handleInputChange}
                          />
                        ) : (
                          <input
                            type={field.type || 'text'}
                            name={field.name}
                            placeholder={field.placeholder}
                            className="w-full bg-white border-2 border-neutral-200 rounded-2xl px-4 py-3 focus:border-black outline-none transition-colors"
                            onChange={handleInputChange}
                          />
                        )}
                      </div>
                    ))}
                    {data.form?.buttonText && (
                      <div className="md:col-span-2">
                        <button
                          type="submit"
                          className="w-full md:w-auto inline-flex items-center justify-center bg-black text-white px-8 py-3 rounded-xl font-bold hover:bg-gray-800 transition-colors"
                        >
                          {data.form.buttonText}
                        </button>
                      </div>
                    )}
                  </form>
                </div>
              )}
            </div>
            <div className="w-full h-[420px] md:h-[520px] bg-neutral-100 rounded-none overflow-hidden shadow-lg">
              {data.mapEmbedUrl ? (
                <iframe
                  src={data.mapEmbedUrl}
                  title={data.mapTitle || 'Location Map'}
                  className="w-full h-full border-0"
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                  allowFullScreen
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center text-gray-500 text-center px-6">
                  Add a `mapEmbedUrl` to show a map here.
                </div>
              )}
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <section
      id={id || data?.id}
      className={clsx(
      "contact-section overflow-hidden",
      variant === 'modern' && 'bg-white',
      variant === 'minimal' && 'bg-neutral-50',
      variant === 'classic' && 'bg-white',
      variant === 'split' && 'bg-white',
      variant === 'card' && 'bg-neutral-50',
      variant === 'map' && 'bg-white',
      variant === 'mapFull' && 'bg-white',
      className
    )}
    >
      {renderVariant()}
    </section>
  );
};

