import React from 'react';
import { ReviewsCarousel as ReviewsCarouselBase } from './ReviewsCarousel';

export const ReviewsCarousel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ReviewsCarouselBase, props)
  );
};
