import React from 'react';
import { ItemShowcaseNeo as ItemShowcaseNeoBase } from './ItemShowcaseNeo';

export const ItemShowcaseNeo = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemShowcaseNeoBase, props)
  );
};
