import React from 'react';
import { TechSpecsPanel as TechSpecsPanelBase } from './TechSpecsPanel';

export const TechSpecsPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(TechSpecsPanelBase, props)
  );
};
