import React from 'react';
import { HeaderFrame as HeaderFrameBase } from './HeaderFrame';

export const HeaderFrame = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HeaderFrameBase, props)
  );
};
