import React from 'react';
import { PathCrumb as PathCrumbBase } from './PathCrumb';

export const PathCrumb = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PathCrumbBase, props)
  );
};
