import React from 'react';
import { BasketElite as BasketEliteBase } from './BasketElite';

export const BasketElite = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BasketEliteBase, props)
  );
};
