import React from 'react';
import { ContactStepEmail as ContactStepEmailBase } from './ContactStepEmail';

export const ContactStepEmail = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ContactStepEmailBase, props)
  );
};
