import React from 'react';

export const OriginLoadingShell = ({ variant = 'split' }) => {
  const baseCard = 'rounded-2xl bg-gray-100 dark:bg-gray-800 border border-gray-200 dark:border-gray-700';
  const shimmer = 'animate-pulse bg-gray-200 dark:bg-gray-700';

  const StatRow = () => (
    <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
      {[0, 1, 2].map((key) => (
        <div key={key} className={`${baseCard} p-4`}>
          <div className={`${shimmer} h-6 w-20 rounded`}></div>
          <div className={`${shimmer} h-4 w-28 rounded mt-3`}></div>
        </div>
      ))}
    </div>
  );

  const ContentBlocks = ({ count = 3 }) => (
    <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
      {Array.from({ length: count }).map((_, idx) => (
        <div key={idx} className={`${baseCard} p-4`}>
          <div className={`${shimmer} h-5 w-1/2 rounded mb-2`}></div>
          <div className={`${shimmer} h-4 w-full rounded mb-2`}></div>
          <div className={`${shimmer} h-4 w-5/6 rounded`}></div>
        </div>
      ))}
    </div>
  );

  const TimelineBlocks = () => (
    <div className="space-y-3">
      {[0, 1, 2].map((key) => (
        <div key={key} className="flex items-start gap-3">
          <div className={`${shimmer} h-10 w-10 rounded-full`}></div>
          <div className={`${baseCard} flex-1 p-4`}>
            <div className={`${shimmer} h-4 w-24 rounded mb-2`}></div>
            <div className={`${shimmer} h-4 w-full rounded mb-2`}></div>
            <div className={`${shimmer} h-4 w-5/6 rounded`}></div>
          </div>
        </div>
      ))}
    </div>
  );

  return (
    <div
      className={`rounded-3xl border ${
        variant === 'minimal' ? 'border-gray-800 bg-black' : 'border-gray-200 bg-white'
      } p-6 sm:p-10 space-y-6`}
    >
      <div className="space-y-3">
        <div className={`${shimmer} h-10 w-28 rounded-full`}></div>
        <div className={`${shimmer} h-8 sm:h-10 w-4/5 rounded`}></div>
        <div className={`${shimmer} h-4 w-3/5 rounded`}></div>
      </div>

      {variant === 'timeline' ? (
        <>
          <StatRow />
          <TimelineBlocks />
        </>
      ) : (
        <>
          <StatRow />
          <ContentBlocks count={variant === 'spotlight' ? 4 : 3} />
        </>
      )}
    </div>
  );
};


