import React from 'react';
import { CollectionGrid as CollectionGridBase } from './CollectionGrid';

export const CollectionGrid = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CollectionGridBase, props)
  );
};
