import React from 'react';
import { PaymentStepMethod as PaymentStepMethodBase } from './PaymentStepMethod';

export const PaymentStepMethod = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PaymentStepMethodBase, props)
  );
};
