import React from 'react';
import { ItemInfoPanel as ItemInfoPanelBase } from './ItemInfoPanel';

export const ItemInfoPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemInfoPanelBase, props)
  );
};
