import React from 'react';
import { TeamSection as TeamSectionBase } from './TeamSection';

export const TeamSection = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(TeamSectionBase, props)
  );
};
