import React from 'react';
import { BasketClassic as BasketClassicBase } from './BasketClassic';

export const BasketClassic = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BasketClassicBase, props)
  );
};
