import React from 'react';
import { PathCrumbs as PathCrumbsBase } from './PathCrumbs';

export const PathCrumbs = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PathCrumbsBase, props)
  );
};
