import React from 'react';
import { SortBar as SortBarBase } from './SortBar';

export const SortBar = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(SortBarBase, props)
  );
};
