import React from 'react';
import { ReachUsPanel as ReachUsPanelBase } from './ReachUsPanel';

export const ReachUsPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ReachUsPanelBase, props)
  );
};
