import React from 'react';
import { StarRating as StarRatingBase } from './StarRating';

export const StarRating = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(StarRatingBase, props)
  );
};
