import React from 'react';
import { HighlightsStack as HighlightsStackBase } from './HighlightsStack';

export const HighlightsStack = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HighlightsStackBase, props)
  );
};
