import React from 'react';
import { PageStepper as PageStepperBase } from './PageStepper';

export const PageStepper = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PageStepperBase, props)
  );
};
