import React from 'react';
import { ArticleNav as ArticleNavBase } from './ArticleNav';

export const ArticleNav = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ArticleNavBase, props)
  );
};
