import React from 'react';
import { FooterLayout as FooterLayoutBase } from './FooterLayout';

export const FooterLayout = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(FooterLayoutBase, props)
  );
};
