import React from 'react';
import { PurchaseFlowBare as PurchaseFlowBareBase } from './PurchaseFlowBare';

export const PurchaseFlowBare = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseFlowBareBase, props)
  );
};
