import React from 'react';
import { PurchaseStepper as PurchaseStepperBase } from './PurchaseStepper';

export const PurchaseStepper = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseStepperBase, props)
  );
};
