import React from 'react';
import { ReviewsTimeline as ReviewsTimelineBase } from './ReviewsTimeline';

export const ReviewsTimeline = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ReviewsTimelineBase, props)
  );
};
