import React from 'react';
import { QuickPeekModal as QuickPeekModalBase } from './QuickPeekModal';

export const QuickPeekModal = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(QuickPeekModalBase, props)
  );
};
