import React from 'react';
import { TrialStepOffer as TrialStepOfferBase } from './TrialStepOffer';

export const TrialStepOffer = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(TrialStepOfferBase, props)
  );
};
