import React from 'react';
import { HighlightsStream as HighlightsStreamBase } from './HighlightsStream';

export const HighlightsStream = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HighlightsStreamBase, props)
  );
};
