import React from 'react';
import { BasketBare as BasketBareBase } from './BasketBare';

export const BasketBare = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BasketBareBase, props)
  );
};
