import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/table-of-contents.css';

/**
 * TableOfContents Component - Highly customizable table of contents with 4 variants
 * @param {Object} props - Component props
 * @param {Object} props.data - TOC configuration data
 * @param {string} [props.data.title] - Title of the TOC section
 * @param {React.ReactNode} [props.data.titleIcon] - Optional icon shown next to the title (defaults to built-in icon)
 * @param {Array} props.data.items - Array of TOC items { id, label, level }
 * @param {string} [props.data.variant='classic'] - 'classic' | 'floating' | 'glass' | 'timeline' | 'minimal'
 * @param {string} [props.data.theme='light'] - 'light' | 'dark'
 * @param {string} [props.data.className] - Additional classes for the container
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const ContentsNavigator = ({ data, className, id, loader = false }) => {
  const [activeId, setActiveId] = useState('');
  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
  }, [loader]);

  useEffect(() => {
    if (data?.items?.length > 0 && !activeId) {
      setActiveId(data.items[0].id);
    }
  }, [data?.items, activeId]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveId(entry.target.id);
          }
        });
      },
      { rootMargin: '-10% 0% -80% 0%' }
    );

    data?.items?.forEach((item) => {
      const element = document.getElementById(item.id);
      if (element) observer.observe(element);
    });

    return () => observer.disconnect();
  }, [data?.items]);

  if (!data) return null;

  const {
    title = 'Table of Contents',
    titleIcon,
    items = [],
    variant = 'classic',
    theme = 'light',
    className: dataClassName
  } = data;

  const isDark = theme === 'dark';

  const DefaultTitleIcon = (
    <svg
      className={clsx('w-4 h-4', isDark ? 'text-gray-400' : 'text-gray-500')}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
      strokeWidth={2}
      aria-hidden="true"
      focusable="false"
    >
      <path strokeLinecap="round" strokeLinejoin="round" d="M4 6h16M4 12h10M4 18h14" />
      <path strokeLinecap="round" strokeLinejoin="round" d="M18 12h2M18 18h2" className="opacity-60" />
    </svg>
  );

  if (showLoader) {
    return (
      <div className={clsx('toc-container p-6', isDark ? 'bg-gray-900' : 'bg-white', className, dataClassName)}>
        <div className="toc-skeleton h-8 w-48 mb-6"></div>
        <div className="space-y-4">
          {[1, 2, 3, 4, 5].map((i) => (
            <div key={i} className="flex items-center gap-3">
              <div className="toc-skeleton h-4 w-4 rounded-full"></div>
              <div className={clsx("toc-skeleton h-4", i % 2 === 0 ? "w-32" : "w-40")}></div>
            </div>
          ))}
        </div>
      </div>
    );
  }

  const renderItems = () => {
    return items.map((item) => (
      <a
        key={item.id}
        href={`#${item.id}`}
        className={clsx(
          'toc-link',
          `toc-level-${item.level || 2}`,
          variant === 'minimal' && 'group',
          activeId === item.id && 'active',
          // For timeline, keep the dot aligned on the main line for all levels (indent only the text via CSS)
          variant === 'timeline' ? 'ml-0' : (item.level === 2 ? 'ml-0' : 'ml-4'),
          variant !== 'minimal' && (isDark ? 'text-gray-400 hover:text-white' : 'text-gray-600 hover:text-black')
        )}
        onClick={(e) => {
          e.preventDefault();
          setActiveId(item.id);
          document.getElementById(item.id)?.scrollIntoView({ behavior: 'smooth' });
        }}
      >
        {variant === 'minimal' && <span className="bullet"></span>}
        <span className="toc-text">{item.label}</span>
      </a>
    ));
  };

  return (
    <nav
      id={id}
      className={clsx(
        'toc-container',
        `toc-variant-${variant}`,
        isDark ? 'dark bg-gray-950 text-white' : 'bg-white text-gray-900',
        className,
        dataClassName
      )}
    >
      <div className={clsx('flex items-center gap-2 mb-4', variant === 'timeline' ? 'px-1' : '')}>
        <span className="flex-shrink-0">{titleIcon ?? DefaultTitleIcon}</span>
        <h3
          className={clsx(
            'leading-none',
            // keep the title compact across variants
            variant === 'timeline' ? 'text-[15px] font-medium' : 'text-base font-semibold',
            isDark ? 'text-gray-200' : 'text-gray-800'
          )}
        >
          {title}
        </h3>
      </div>
      <div className="flex flex-col gap-1">
        {renderItems()}
      </div>
    </nav>
  );
};

