import React from 'react';
import { CollectionMasonry as CollectionMasonryBase } from './CollectionMasonry';

export const CollectionMasonry = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CollectionMasonryBase, props)
  );
};
