import React from 'react';
import { CompareTable as CompareTableBase } from './CompareTable';

export const CompareTable = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CompareTableBase, props)
  );
};
