import React from 'react';
import { HighlightsElite as HighlightsEliteBase } from './HighlightsElite';

export const HighlightsElite = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HighlightsEliteBase, props)
  );
};
