import React from 'react';
import { StoryPanel as StoryPanelBase } from './StoryPanel';

export const StoryPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(StoryPanelBase, props)
  );
};
