

const SectionHeader = ({ badge, title, subtitle, align = 'left' }) => (
  <div className={`space-y-3 ${align === 'center' ? 'text-center' : ''}`}>
    <div className="inline-flex items-center gap-2 px-3 py-1.5 rounded-full bg-white/5 border border-white/10 text-xs font-semibold uppercase tracking-[0.08em] text-gray-200">
      <span className="h-2 w-2 rounded-full bg-primary-400"></span>
      {badge}
    </div>
    <h2 className="text-3xl sm:text-4xl font-extrabold text-white leading-tight">{title}</h2>
    {subtitle && <p className="text-gray-300 max-w-3xl">{subtitle}</p>}
  </div>
);

const StatPill = ({ label, value }) => (
  <div className="flex flex-col items-start rounded-2xl border border-white/10 bg-white/5 px-4 py-3 sm:px-6 sm:py-4">
    <span className="text-2xl sm:text-3xl font-bold text-white">{value}</span>
    <span className="text-sm text-gray-300">{label}</span>
  </div>
);

const ValueCard = ({ title, description }) => (
  <div className="bg-gray-900/60 p-5 hover:border-primary-500/60 transition-all duration-200 shadow-sm">
    <div className="flex items-center gap-3 mb-3">
      <span className="h-10 w-10 rounded-full bg-primary-600/20 text-primary-300 flex items-center justify-center font-semibold">
        {title.charAt(0)}
      </span>
      <h3 className="text-lg font-semibold text-white">{title}</h3>
    </div>
    <p className="text-gray-400 leading-relaxed">{description}</p>
  </div>
);

// Variant 1: Split narrative + stats
const SplitAbout = ({ data }) => (
  <section className="relative overflow-hidden bg-gradient-to-br from-black via-gray-900 to-gray-950 text-white">
    <div className="container mx-auto">
    <div className="absolute inset-0 bg-[radial-gradient(circle_at_30%_20%,rgba(59,130,246,0.14),transparent_35%),radial-gradient(circle_at_80%_0%,rgba(79,70,229,0.12),transparent_30%)]"></div>
    <div className="relative grid grid-cols-1 lg:grid-cols-2 gap-10 p-6 sm:p-10 lg:p-14">
      <div className="space-y-6">
        <SectionHeader badge={data.badge} title={data.title} subtitle={data.subtitle} />
        <p className="text-lg text-gray-300 leading-relaxed">{data.description}</p>
        <div className="flex flex-wrap gap-3">
          <a
            href={data.cta?.primary?.href}
            className="px-5 py-3 rounded-xl bg-primary-600 text-white font-semibold shadow-lg shadow-primary-900/30 hover:bg-primary-500 transition"
          >
            {data.cta?.primary?.label}
          </a>
          <a
            href={data.cta?.secondary?.href}
            className="px-5 py-3 rounded-xl border border-white/15 text-white/90 font-semibold hover:border-primary-500/60 hover:text-white transition"
          >
            {data.cta?.secondary?.label}
          </a>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 sm:gap-4 pt-4">
          {data.stats?.map((stat, idx) => (
            <StatPill key={idx} label={stat.label} value={stat.value} />
          ))}
        </div>
      </div>
      <div className="bg-gray-900/60 border border-gray-800 rounded-2xl p-6 sm:p-8 space-y-6 shadow-xl">
        <div className="flex items-center justify-between">
          <h3 className="text-xl font-semibold text-white">Why teams choose us</h3>
          <span className="px-3 py-1 rounded-full bg-primary-600/20 text-primary-200 text-xs font-semibold border border-primary-500/40">
            Latest
          </span>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          {data.highlights?.map((item, idx) => (
            <div
              key={idx}
              className="bg-gray-950/70 p-4 hover:border-primary-600/50 transition-all duration-200"
            >
              <h4 className="text-white font-semibold mb-2">{item.title}</h4>
              <p className="text-gray-400 text-sm leading-relaxed">{item.description}</p>
            </div>
          ))}
        </div>
        <div className="rounded-xl bg-gradient-to-r from-gray-800 to-gray-900 border border-gray-700/60 p-5 space-y-3">
          <h4 className="text-white font-semibold">Mission</h4>
          <p className="text-gray-300 leading-relaxed">{data.mission}</p>
          </div>
        </div>
      </div>
    </div>
  </section>
);

// Variant 2: Spotlight with values
const SpotlightAbout = ({ data }) => (
  <section className="relative overflow-hidden">
    <div className="container mx-auto">
    <div className="absolute inset-0 bg-[radial-gradient(circle_at_70%_20%,rgba(59,130,246,0.18),transparent_40%)]"></div>
    <div className="relative p-6 sm:p-10 lg:p-14 space-y-10">
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
        <SectionHeader badge={data.badge} title="A studio built for momentum" subtitle={data.subtitle} />
        <div className="flex flex-wrap gap-3">
          {data.stats?.slice(0, 3).map((stat, idx) => (
            <div
              key={idx}
              className="px-4 py-3 rounded-2xl border border-white/10 bg-white/5 min-w-[140px] shadow-sm"
            >
              <div className="text-2xl font-bold text-primary-200">{stat.value}</div>
              <div className="text-xs text-gray-300 uppercase tracking-wide">{stat.label}</div>
            </div>
          ))}
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2 rounded-2xl border border-white/10 bg-white/5 p-6 sm:p-8 space-y-6 shadow-xl">
          <div className="flex items-center gap-3">
            <span className="h-10 w-10 rounded-full bg-primary-500/25 flex items-center justify-center text-primary-200 font-semibold">
              UX
            </span>
            <div>
              <p className="text-sm text-gray-300 uppercase tracking-[0.08em]">Narrative</p>
              <h3 className="text-xl sm:text-2xl font-bold text-white">Design that ships faster</h3>
            </div>
          </div>
          <p className="text-gray-300 leading-relaxed">{data.description}</p>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            {data.values?.map((value, idx) => (
              <ValueCard key={idx} title={value.title} description={value.description} />
            ))}
          </div>
        </div>

        <div className="rounded-2xl border border-primary-500/30 bg-gradient-to-b from-primary-900/50 via-gray-900 to-black p-6 sm:p-8 space-y-5 shadow-lg">
          <div className="flex items-center gap-2 text-primary-100 text-sm font-semibold uppercase tracking-[0.12em]">
            <span className="h-2 w-2 rounded-full bg-primary-300"></span>
            Leadership
          </div>
          <div className="space-y-4">
            {data.team?.map((member, idx) => (
              <div key={idx} className="rounded-xl border border-white/10 bg-white/5 p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <div className="text-white font-semibold">{member.name}</div>
                    <div className="text-gray-300 text-sm">{member.role}</div>
                  </div>
                  <span className="px-3 py-1 text-xs rounded-full bg-primary-600/30 text-primary-100 border border-primary-500/40">
                    {member.focus}
                  </span>
                </div>
              </div>
            ))}
          </div>
          <div className="pt-2">
            <a
              href={data.cta?.primary?.href}
              className="inline-flex items-center justify-center w-full rounded-xl bg-white text-gray-900 font-semibold py-3 hover:bg-gray-100 transition"
            >
              {data.cta?.primary?.label}
            </a>
          </div>
          </div>
        </div>
      </div>
    </div>
  </section>
);

// Variant 3: Timeline
const TimelineAbout = ({ data }) => (
  <section className="overflow-hidden">
    <div className="container mx-auto">
    <div className="px-6 sm:px-10 pt-10 pb-6 bg-gradient-to-r from-gray-900 via-black to-gray-900 text-white">
      <SectionHeader badge="Our story" title="Momentum over the years" subtitle={data.subtitle} />
    </div>
    <div className="p-6 sm:p-10 space-y-8">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {data.stats?.map((stat, idx) => (
          <div
            key={idx}
            className="rounded-xl border border-gray-200 bg-gray-50 px-5 py-4 flex items-center gap-4"
          >
            <div className="h-12 w-12 rounded-full bg-primary-50 text-primary-700 flex items-center justify-center font-bold">
              {idx + 1}
            </div>
            <div>
              <div className="text-xl font-semibold text-gray-900">{stat.value}</div>
              <div className="text-sm text-gray-600">{stat.label}</div>
            </div>
          </div>
        ))}
      </div>
      <div className="relative">
        <div className="absolute left-5 sm:left-8 top-0 bottom-0 w-px bg-gradient-to-b from-primary-200 via-gray-200 to-primary-200"></div>
        <div className="space-y-6">
          {data.timeline?.map((item, idx) => (
            <div key={idx} className="relative pl-12 sm:pl-16">
              <div className="absolute left-3 sm:left-6 top-3 h-3 w-3 rounded-full bg-primary-500 shadow-[0_0_0_6px_rgba(59,130,246,0.15)]"></div>
              <div className="rounded-2xl border border-gray-200 bg-white p-5 shadow-sm hover:shadow-md transition">
                <div className="flex items-center justify-between">
                  <span className="text-sm font-semibold text-primary-700 uppercase tracking-[0.12em]">
                    {item.year}
                  </span>
                  <span className="px-3 py-1 rounded-full bg-gray-100 text-gray-700 text-xs font-semibold">
                    {item.title}
                  </span>
                </div>
                <p className="text-gray-600 mt-3 leading-relaxed">{item.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>    </div>
    </div>
  </section>
);

// Variant 4: Stacked cards
const StackedAbout = ({ data }) => (
  <section className="overflow-hidden">
    <div className="container mx-auto">
    <div className="p-6 sm:p-10 lg:p-12 space-y-8">
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
        <div className="max-w-2xl">
          <div className="text-sm font-semibold text-primary-600 uppercase tracking-[0.12em] mb-2">{data.badge}</div>
          <h2 className="text-3xl sm:text-4xl font-bold text-gray-900 leading-tight">{data.title}</h2>
          <p className="text-gray-600 mt-3 leading-relaxed">{data.mission}</p>
        </div>
        <div className="flex gap-3 flex-wrap">
          {data.stats?.slice(0, 2).map((stat, idx) => (
            <div key={idx} className="rounded-xl border border-primary-100 bg-primary-50 px-5 py-4">
              <div className="text-2xl font-extrabold text-primary-700">{stat.value}</div>
              <div className="text-xs text-primary-900/70 font-semibold uppercase tracking-[0.08em]">
                {stat.label}
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {data.highlights?.map((item, idx) => (
          <div
            key={idx}
            className="rounded-2xl border border-gray-200 bg-white p-5 shadow-sm hover:shadow-md transition"
          >
            <div className="flex items-center justify-between mb-3">
              <span className="h-10 w-10 rounded-full bg-gray-900 text-white flex items-center justify-center font-semibold">
                {idx + 1}
              </span>
              <span className="text-xs font-semibold text-primary-700 uppercase tracking-[0.1em]">
                Highlight
              </span>
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">{item.title}</h3>
            <p className="text-gray-600 leading-relaxed">{item.description}</p>
          </div>
        ))}
      </div>

      <div className="rounded-2xl border border-gray-200 bg-white p-6 lg:p-8 grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="space-y-4">
          <h3 className="text-xl font-bold text-gray-900">How we work</h3>
          <p className="text-gray-600 leading-relaxed">
            We align on outcomes, design in public, and ship iterative releases. Every sprint ships a tangible,
            measurable improvement.
          </p>
          <div className="flex gap-3">
            <a
              href={data.cta?.primary?.href}
              className="px-5 py-3 rounded-xl bg-gray-900 text-white font-semibold hover:bg-black transition"
            >
              {data.cta?.primary?.label}
            </a>
            <a
              href={data.cta?.secondary?.href}
              className="px-5 py-3 rounded-xl border border-gray-200 text-gray-900 font-semibold hover:border-gray-300 transition"
            >
              {data.cta?.secondary?.label}
            </a>
          </div>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          {data.values?.slice(0, 4).map((value, idx) => (
            <div
              key={idx}
              className="rounded-xl border border-gray-200 bg-gray-50 p-4 hover:border-primary-200 transition"
            >
              <div className="text-sm font-semibold text-primary-700 uppercase tracking-[0.08em] mb-1">
                {value.title}
              </div>
              <p className="text-gray-600 text-sm leading-relaxed">{value.description}</p>
            </div>
          ))}
        </div>
      </div>
    </div>    </div>
  </section>
);

// Variant 5: Minimal spotlight
