// Variant 7: Image + timeline + cards (matches requested reference)
const StoryTimelineAbout = ({ data }) => (
  <section className="bg-gradient-to-b from-gray-50 via-white to-gray-50 text-gray-900 overflow-hidden">
    <div className="container mx-auto">
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-0">
      {/* Left: Story + Timeline */}
      <div className="p-6 sm:p-10 lg:p-14 space-y-6">
        <div className="space-y-3">
          <div className="text-sm font-semibold text-primary-700 uppercase tracking-[0.14em]">{data.badge}</div>
          <h2 className="text-3xl sm:text-4xl font-extrabold leading-snug text-gray-900">{data.title}</h2>
          <p className="text-gray-600 leading-relaxed">{data.storyIntro || data.description}</p>
        </div>

        <div className="mt-6 space-y-6">
          {data.timeline?.map((item, idx) => (
            <div key={idx} className="relative pl-12">
              <div className="absolute left-0 top-1.5 bottom-0 w-px bg-primary-200" aria-hidden />
              <div className="absolute left-[-7px] top-1.5 h-3.5 w-3.5 rounded-full bg-primary-500 shadow-[0_0_0_6px_rgba(59,130,246,0.12)]" aria-hidden />
              <div className="flex flex-col gap-1">
                <span className="text-base font-bold text-gray-900">{item.year}</span>
                {item.title && <span className="text-sm font-semibold text-primary-700">{item.title}</span>}
                <p className="text-gray-600 leading-relaxed">{item.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Right: Image + cards */}
      <div className="relative bg-gray-900">
        <div className="absolute inset-0">
          <img
            src={data.heroImage}
            alt={data.heroImageAlt}
            className="w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-b from-black/20 via-black/10 to-black/30"></div>
        </div>
        <div className="relative h-full flex flex-col justify-end gap-4 p-6 sm:p-8 lg:p-10">
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            {data.highlights?.slice(0, 2).map((item, idx) => (
              <div
                key={idx}
                className="rounded-2xl bg-white shadow-md border border-gray-200 p-4 sm:p-5"
              >
                <div className="flex items-center gap-2 mb-2">
                  <span className="h-2 w-2 rounded-full bg-primary-500"></span>
                  <span className="text-sm font-semibold text-gray-900">{item.title}</span>
                </div>
                <p className="text-sm text-gray-600 leading-relaxed">{item.description}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
    </div>
  </section>
);

const variantComponents = {
  split: SplitAbout,
  spotlight: SpotlightAbout,
  timeline: TimelineAbout,
  stacked: StackedAbout,
  minimal: MinimalAbout,
  image: ImageAbout,
  story: StoryTimelineAbout,
};

export const OriginPanel = ({ data = {}, variant, loader = false }) => {
  const mergedData = {
    ...data,
    stats: data.stats || [],
    highlights: data.highlights || [],
    values: data.values || [],
    timeline: data.timeline || [],
    team: data.team || [],
    cta: { ...(data.cta || {}) },
    heroImage: data.heroImage || '',
    heroImageAlt: data.heroImageAlt || '',
    storyIntro: data.storyIntro || '',
  };

  const variantKey = mergedData.variant || variant || 'split';
  const Component = variantComponents[variantKey] || SplitAbout;

  if (loader) {
    return (
        <div className="bg-gradient-to-br from-black via-gray-900 to-gray-950 p-6 sm:p-10 animate-pulse">
          <div className="space-y-4">
            <div className="h-10 w-32 rounded-full bg-white/10"></div>
            <div className="h-10 sm:h-12 w-5/6 bg-white/10 rounded-xl"></div>
            <div className="h-4 w-4/6 bg-white/10 rounded"></div>
          </div>
          <div className="mt-8 grid grid-cols-1 sm:grid-cols-3 gap-4">
            {[1, 2, 3].map((i) => (
              <div key={i} className="h-24 rounded-2xl bg-white/5 border border-white/10"></div>
            ))}
          </div>
          <div className="mt-8 grid grid-cols-1 sm:grid-cols-2 gap-4">
            {[1, 2, 3, 4].map((i) => (
              <div key={i} className="h-28 rounded-xl bg-white/5 border border-white/10"></div>
            ))}
          </div>
        </div>
    );
  }

  return (
      <Component data={mergedData} />
  );
};


