import React from 'react';
import { HelpCenter as HelpCenterBase } from './HelpCenter';

export const HelpCenter = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HelpCenterBase, props)
  );
};
