import React from 'react';
import { ReachUsLoadingShell as ReachUsLoadingShellBase } from './ReachUsLoadingShell';

export const ReachUsLoadingShell = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ReachUsLoadingShellBase, props)
  );
};
