import { useEffect, useMemo, useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * BradCrump Component - Breadcrumb navigation with image-header variants + inline variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Breadcrumb configuration data
 * @param {string} [props.data.layout='inline'] - Layout: 'inline' | 'image'
 * @param {string} [props.data.variant='image-1'] - Variant
 *  Inline variants:
 *   'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray' | 'icon-dark' | 'glass' | 'outlined'
 *  Image variants (10):
 *   'image-1' | 'image-2' | 'image-3' | 'image-4' | 'image-5' | 'image-6' | 'image-7' | 'image-8' | 'image-9' | 'image-10'
 * @param {Array} [props.data.items=[]] - Breadcrumb items array: { label, href?, active? }
 * @param {string} [props.data.separator='/'] - Text separator (used in a few variants)
 * @param {boolean} [props.data.collapseOnMobile=true] - Collapse long breadcrumbs on mobile into: first /  / last
 * @param {string} [props.data.title] - (image layout) Main title text
 * @param {string} [props.data.subtitle] - (image layout) Subtitle text
 * @param {string} [props.data.description] - (image layout) Supporting description text
 * @param {string} [props.data.badge] - (image layout) Small badge chip above title
 * @param {string} [props.data.heroImage] - (image layout) Background image URL
 * @param {string} [props.data.heroImageAlt] - (image layout) Background image alt
 * @param {string} [props.data.size='md'] - (image layout) Header size: 'sm' | 'md' | 'lg'
 * @param {Function} [props.onItemClick] - Callback when item is clicked (prevents default)
 * @param {boolean} [props.loader=false] - Show skeleton loader (auto-hides after 2s)
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - Optional id
 */
export const BradCrump = ({
  data = {},
  onItemClick,
  loader = false,
  className,
  id,
}) => {
  const {
    layout: layoutProp,
    variant: variantProp,
    items = [
      { label: 'Home', href: '/' },
      { label: 'Products', href: '/products' },
      { label: 'Current Page', href: '#', active: true },
    ],
    separator = '/',
    collapseOnMobile = true,
    // image layout
    title,
    subtitle,
    description,
    badge,
    heroImage,
    heroImageAlt,
    size = 'md',
  } = data;

  const layout = layoutProp || (heroImage ? 'image' : 'inline');
  const variant = variantProp || (layout === 'image' ? 'image-1' : 'default');

  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      setShowLoader(true);
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
    setShowLoader(false);
  }, [loader]);

  const isInlineDark = variant === 'dark' || variant === 'dark-gray' || variant === 'icon-dark';

  const displayItems = useMemo(() => {
    if (!collapseOnMobile) return items;
    if (!Array.isArray(items)) return [];
    if (items.length <= 3) return items;
    // We render a responsive ellipsis element; keep the data simple here.
    return items;
  }, [items, collapseOnMobile]);

  const inlineContainerClasses = {
    default: 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm',
    modern: 'flex items-center flex-wrap gap-2 sm:gap-3 text-sm',
    minimal: 'flex items-center flex-wrap gap-1 text-xs',
    compact: 'flex items-center flex-wrap gap-0.5 sm:gap-1 text-xs',
    icon: 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm',
    dark: 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm',
    'dark-gray': 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm',
    'icon-dark': 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm',
    glass: 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm px-3 py-2 rounded-full border border-white/50 bg-white/60 backdrop-blur-md shadow-sm',
    outlined: 'flex items-center flex-wrap gap-1 sm:gap-2 text-xs sm:text-sm',
  };

  const inlineItemClasses = {
    default: clsx('text-gray-600 hover:text-gray-900 transition-colors', 'active:text-gray-900 font-medium'),
    modern: clsx('text-gray-600 hover:text-primary-600 transition-colors font-medium', 'active:text-gray-900'),
    minimal: clsx('text-gray-500 hover:text-gray-700 transition-colors', 'active:text-gray-900'),
    compact: clsx('text-gray-500 hover:text-gray-700 transition-colors', 'active:text-gray-900'),
    icon: clsx('text-gray-600 hover:text-primary-600 transition-colors', 'active:text-gray-900'),
    dark: clsx('text-gray-400 hover:text-gray-200 transition-colors', 'active:text-white font-medium'),
    'dark-gray': clsx('text-gray-500 hover:text-gray-300 transition-colors', 'active:text-gray-100'),
    'icon-dark': clsx('text-gray-400 hover:text-gray-200 transition-colors', 'active:text-white font-medium'),
    glass: clsx('text-gray-700 hover:text-gray-950 transition-colors', 'active:text-gray-950 font-semibold'),
    outlined: clsx('text-gray-700 hover:text-gray-950 transition-colors', 'active:text-white'),
  };

  const heightClasses = {
    sm: 'min-h-[180px] sm:min-h-[220px] lg:min-h-[260px]',
    md: 'min-h-[220px] sm:min-h-[280px] lg:min-h-[340px]',
    lg: 'min-h-[280px] sm:min-h-[360px] lg:min-h-[440px]',
  };

  const ChevronIcon = ({ tone = 'light' } = {}) => (
    <svg
      className={clsx('w-4 h-4', tone === 'dark' ? 'text-white/65' : tone === 'glass' ? 'text-slate-600/70' : 'text-gray-400')}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
      aria-hidden="true"
    >
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
    </svg>
  );

  const HomeIcon = ({ tone = 'light' } = {}) => (
    <svg
      className={clsx('w-4 h-4', tone === 'dark' ? 'text-white/75' : tone === 'glass' ? 'text-slate-700/80' : 'text-gray-400')}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
      aria-hidden="true"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth={2}
        d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6"
      />
    </svg>
  );

  const SeparatorText = ({ tone = 'light' } = {}) => (
    <span className={clsx('px-1', tone === 'dark' ? 'text-white/55' : tone === 'glass' ? 'text-slate-500/70' : 'text-gray-300')} aria-hidden="true">
      {separator}
    </span>
  );

  const renderCrumbs = ({ tone = 'light', pill = false, showHomeIcon = false } = {}) => {
    const linkCls =
      tone === 'dark'
        ? 'text-white/80 hover:text-white transition-colors'
        : tone === 'glass'
          ? 'text-slate-700/90 hover:text-slate-900 transition-colors'
          : 'text-gray-600 hover:text-gray-900 transition-colors';

    const activeCls =
      tone === 'dark'
        ? 'text-white font-semibold'
        : tone === 'glass'
          ? 'text-slate-900 font-semibold'
          : 'text-gray-900 font-semibold';

    const wrapCls = pill
      ? clsx(
          'inline-flex items-center gap-1 sm:gap-2 rounded-full px-3 py-1.5',
          tone === 'dark'
            ? 'bg-white/10 border border-white/15 backdrop-blur-md'
            : 'bg-white/70 border border-white/60 shadow-sm backdrop-blur-md'
        )
      : 'flex items-center flex-wrap gap-1 sm:gap-2';

    return (
      <nav aria-label="Breadcrumb">
        <ol className={wrapCls}>
          {displayItems.map((item, index) => {
            const isLast = index === displayItems.length - 1;
            const isActive = Boolean(item?.active) || isLast;
            const label = item?.label ?? '';

            // Mobile collapse: first /  / last (only visually on mobile)
            const shouldShowEllipsis =
              collapseOnMobile &&
              Array.isArray(displayItems) &&
              displayItems.length > 3 &&
              index === 1;
            const shouldHideMiddleOnMobile =
              collapseOnMobile &&
              Array.isArray(displayItems) &&
              displayItems.length > 3 &&
              index > 0 &&
              index < displayItems.length - 1;

            if (shouldShowEllipsis) {
              return (
                <li key="ellipsis" className="flex items-center">
                  <span className={clsx('inline-flex sm:hidden items-center', tone === 'dark' ? 'text-white/70' : 'text-gray-500')}>
                    <ChevronIcon tone={tone} />
                    <span className="px-1"></span>
                    <ChevronIcon tone={tone} />
                  </span>
                </li>
              );
            }

            const content = item?.href && !isActive ? (
              <a
                href={item.href}
                onClick={(e) => {
                  if (onItemClick) {
                    e.preventDefault();
                    onItemClick(item, index);
                  }
                }}
                className={linkCls}
              >
                {label}
              </a>
            ) : (
              <span className={activeCls} aria-current={isActive ? 'page' : undefined}>
                {label}
              </span>
            );

            return (
              <li key={`${label}-${index}`} className={clsx('flex items-center gap-1 sm:gap-2', shouldHideMiddleOnMobile && 'hidden sm:flex')}>
                {index === 0 && showHomeIcon && (
                  <span className="inline-flex items-center">
                    <HomeIcon tone={tone} />
                  </span>
                )}

                {content}

                {!isLast && !collapseOnMobile && (
                  <span className="inline-flex items-center">
                    <ChevronIcon tone={tone} />
                  </span>
                )}

                {!isLast && collapseOnMobile && (
                  <span className="hidden sm:inline-flex items-center">
                    <ChevronIcon tone={tone} />
                  </span>
                )}
              </li>
            );
          })}
        </ol>
      </nav>
    );
  };

  const InlineSkeleton = () => {
    const skeletonBg = isInlineDark ? 'bg-gray-700' : 'bg-gray-200';
    const skeletonDot = isInlineDark ? 'bg-gray-600' : 'bg-gray-300';

    return (
      <div
        id={id}
        className={clsx(inlineContainerClasses[variant] || inlineContainerClasses.default, className)}
        aria-busy="true"
        aria-live="polite"
      >
        <div className="flex items-center gap-2 sm:gap-3">
          <div className={clsx('h-4 w-4 rounded-full product-skeleton', skeletonDot)} />
          <div className={clsx('h-4 w-16 sm:w-20 rounded-md product-skeleton', skeletonBg)} />
          <div className={clsx('h-4 w-4 rounded-full product-skeleton', skeletonDot)} />
          <div className={clsx('h-4 w-20 sm:w-24 rounded-md product-skeleton', skeletonBg)} />
          <div className={clsx('h-4 w-4 rounded-full product-skeleton', skeletonDot)} />
          <div className={clsx('h-4 w-24 sm:w-32 rounded-md product-skeleton', skeletonBg)} />
        </div>
      </div>
    );
  };

  const ImageSkeleton = () => (
    <section
      id={id}
      className={clsx(
        'relative w-full overflow-hidden border border-gray-200 bg-white',
        heightClasses[size] || heightClasses.md,
        className
      )}
      aria-busy="true"
      aria-live="polite"
    >
      <div className="absolute inset-0 bg-gray-200 product-skeleton" />
      <div className="absolute inset-0 bg-gradient-to-t from-black/40 via-black/15 to-black/5" />
      <div className="absolute inset-0 bg-gradient-to-br from-white/0 via-white/0 to-white/10" />
      <div className="relative z-10 h-full w-full flex items-end">
        <div className="w-full px-5 sm:px-8 lg:px-12 pb-6 sm:pb-10">
          <div className="flex flex-col gap-3 max-w-3xl">
            {/* Badge */}
            <div className="flex items-center gap-2">
              <div className="h-2.5 w-2.5 rounded-full product-skeleton bg-white/25" />
              <div className="h-6 w-24 rounded-full product-skeleton bg-white/25" />
            </div>

            {/* Breadcrumb pill */}
            <div className="inline-flex w-full max-w-[520px] items-center gap-2 rounded-full border border-white/10 bg-black/10 px-3 py-2 backdrop-blur-sm">
              <div className="h-4 w-4 rounded-full product-skeleton bg-white/20" />
              <div className="h-3.5 w-16 rounded product-skeleton bg-white/20" />
              <div className="h-3.5 w-3.5 rounded-full product-skeleton bg-white/20" />
              <div className="h-3.5 w-20 rounded product-skeleton bg-white/20" />
              <div className="h-3.5 w-3.5 rounded-full product-skeleton bg-white/20" />
              <div className="h-3.5 w-24 rounded product-skeleton bg-white/20" />
            </div>

            {/* Title / subtitle / description */}
            <div className="h-10 w-72 sm:w-[520px] rounded-lg product-skeleton bg-white/25" />
            <div className="h-5 w-64 sm:w-[420px] rounded-md product-skeleton bg-white/20" />
            <div className="mt-1 space-y-2">
              <div className="h-4 w-80 sm:w-[560px] rounded product-skeleton bg-white/15" />
              <div className="h-4 w-64 sm:w-[420px] rounded product-skeleton bg-white/15" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );

  if (layout === 'image') {
    const styles = {
      'image-1': {
        tone: 'dark',
        overlay: 'bg-gradient-to-t from-black/75 via-black/35 to-black/10',
        align: 'items-end text-left',
        crumbPill: true,
        deco: [
          'pointer-events-none absolute -top-20 -right-20 h-56 w-56 rounded-full bg-primary-500/25 blur-3xl motion-safe:animate-pulse',
          'pointer-events-none absolute -bottom-24 -left-16 h-64 w-64 rounded-full bg-emerald-400/20 blur-3xl motion-safe:animate-pulse [animation-delay:600ms]',
          'pointer-events-none absolute inset-0 opacity-[0.12] bg-[radial-gradient(circle_at_20%_20%,white_0,transparent_35%),radial-gradient(circle_at_80%_30%,white_0,transparent_40%)]',
        ],
        title: 'text-4xl sm:text-6xl lg:text-7xl font-extrabold tracking-tight text-white drop-shadow-[0_18px_48px_rgba(0,0,0,0.55)]',
        subtitle: 'text-white/90 text-base sm:text-lg',
        desc: 'text-white/80 text-sm sm:text-base max-w-2xl',
      },
      'image-2': {
        tone: 'dark',
        overlay: 'bg-gradient-to-r from-black/80 via-black/35 to-transparent',
        align: 'items-center text-left',
        crumbPill: true,
        deco: [
          'pointer-events-none absolute inset-0 opacity-[0.18] bg-[linear-gradient(to_right,rgba(255,255,255,0.18)_1px,transparent_1px),linear-gradient(to_bottom,rgba(255,255,255,0.18)_1px,transparent_1px)] bg-[size:28px_28px]',
          'pointer-events-none absolute -top-24 left-1/4 h-64 w-64 rounded-full bg-purple-400/20 blur-3xl',
        ],
        title: 'text-3xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight text-white',
        subtitle: 'text-white/85 text-base sm:text-lg',
        desc: 'text-white/80 text-sm sm:text-base max-w-xl',
      },
      'image-3': {
        tone: 'glass',
        overlay: 'bg-gradient-to-t from-black/35 via-black/10 to-transparent',
        align: 'items-end text-left',
        panel: 'bg-white/75 backdrop-blur-xl border border-white/60 shadow-xl rounded-2xl p-5 sm:p-7',
        crumbPill: false,
        deco: [
          'pointer-events-none absolute -bottom-24 -right-20 h-64 w-64 rounded-full bg-white/30 blur-3xl',
          'pointer-events-none absolute inset-0 opacity-[0.10] bg-[radial-gradient(circle_at_10%_90%,rgba(255,255,255,0.35)_0,transparent_35%)]',
        ],
        title: 'text-3xl sm:text-5xl lg:text-6xl font-bold tracking-tight text-slate-900',
        subtitle: 'text-slate-700 text-sm sm:text-base',
        desc: 'text-slate-600 text-sm sm:text-base max-w-2xl',
      },
      'image-4': {
        tone: 'dark',
        overlay: 'bg-gradient-to-b from-black/60 via-black/45 to-black/60',
        align: 'items-center text-center',
        crumbPill: true,
        badge: 'bg-white/10 border border-white/15 text-white',
        deco: [
          'pointer-events-none absolute inset-0 opacity-[0.18] bg-[radial-gradient(circle_at_50%_20%,rgba(255,255,255,0.28)_0,transparent_55%)]',
        ],
        title: 'text-5xl sm:text-6xl lg:text-7xl font-extralight tracking-tight text-white',
        subtitle: 'text-white/85 text-base sm:text-lg',
        desc: 'text-white/80 text-sm sm:text-base max-w-2xl mx-auto',
      },
      'image-5': {
        tone: 'glass',
        overlay: 'bg-gradient-to-tr from-white/70 via-white/25 to-transparent',
        align: 'items-end text-left',
        panel: 'bg-white/70 backdrop-blur-md border border-white/80 shadow-sm rounded-2xl px-5 py-4 sm:px-7 sm:py-6',
        crumbPill: false,
        deco: [
          'pointer-events-none absolute inset-0 opacity-[0.10] bg-[linear-gradient(to_right,rgba(15,23,42,0.10)_1px,transparent_1px),linear-gradient(to_bottom,rgba(15,23,42,0.10)_1px,transparent_1px)] bg-[size:34px_34px]',
        ],
        title: 'text-3xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight text-slate-900',
        subtitle: 'text-slate-700 text-sm sm:text-base',
        desc: 'text-slate-600 text-sm sm:text-base max-w-2xl',
      },
      'image-6': {
        tone: 'dark',
        overlay: 'bg-gradient-to-br from-black/75 via-black/35 to-black/5',
        align: 'items-end text-left',
        frame: 'border border-white/20 rounded-2xl p-5 sm:p-7 shadow-[0_18px_60px_-34px_rgba(0,0,0,0.8)]',
        crumbPill: false,
        deco: [
          'pointer-events-none absolute inset-0 opacity-[0.10] bg-[radial-gradient(circle_at_15%_25%,rgba(255,255,255,0.22)_0,transparent_38%),radial-gradient(circle_at_80%_70%,rgba(255,255,255,0.18)_0,transparent_42%)]',
        ],
        title: 'text-3xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight text-white',
        subtitle: 'text-white/85 text-sm sm:text-base',
        desc: 'text-white/80 text-sm sm:text-base max-w-2xl',
      },
      'image-7': {
        tone: 'dark',
        overlay: 'bg-gradient-to-t from-black/75 via-black/35 to-transparent',
        align: 'items-end text-left',
        accent: 'before:absolute pl-10 before:left-0 before:top-0 before:h-full before:w-1.5 before:bg-primary-500 before:rounded-full',
        crumbPill: true,
        deco: [
          'pointer-events-none absolute -top-28 -right-24 h-72 w-72 rounded-full bg-primary-500/18 blur-3xl',
          'pointer-events-none absolute inset-0 opacity-[0.12] bg-[linear-gradient(to_bottom,rgba(255,255,255,0.12)_1px,transparent_1px)] bg-[size:1px_18px]',
        ],
        title: 'text-3xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight text-white',
        subtitle: 'text-white/85 text-sm sm:text-base',
        desc: 'text-white/80 text-sm sm:text-base max-w-2xl',
      },
      'image-8': {
        tone: 'glass',
        overlay: 'bg-gradient-to-b from-black/10 via-black/10 to-black/30',
        align: 'items-center text-left',
        panel: 'bg-white/70 backdrop-blur-xl border border-white/70 shadow-xl rounded-full px-6 py-4 sm:px-8 sm:py-5',
        crumbPill: false,
        deco: [
          'pointer-events-none absolute inset-0 opacity-[0.10] bg-[radial-gradient(circle_at_0%_50%,rgba(255,255,255,0.45)_0,transparent_40%)]',
        ],
        title: 'text-2xl sm:text-4xl lg:text-5xl font-bold tracking-tight text-slate-900',
        subtitle: 'text-slate-700 text-sm sm:text-base',
        desc: 'text-slate-600 text-sm sm:text-base max-w-xl',
      },
      'image-9': {
        tone: 'dark',
        overlay: 'bg-gradient-to-l from-black/80 via-black/35 to-transparent',
        align: 'items-center text-right',
        crumbPill: true,
        deco: [
          'pointer-events-none absolute -bottom-24 -right-24 h-72 w-72 rounded-full bg-rose-400/20 blur-3xl',
        ],
        title: 'text-3xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight text-white',
        subtitle: 'text-white/85 text-sm sm:text-base',
        desc: 'text-white/80 text-sm sm:text-base max-w-xl ml-auto',
      },
      'image-10': {
        tone: 'dark',
        overlay: 'bg-black/45',
        align: 'items-end text-left',
        crumbPill: false,
        minimal: true,
        deco: [
          'pointer-events-none absolute inset-0 opacity-[0.20] bg-[radial-gradient(circle_at_50%_0%,rgba(255,255,255,0.22)_0,transparent_55%)]',
        ],
        title: 'text-5xl sm:text-6xl lg:text-7xl font-extralight tracking-tight text-white',
        subtitle: 'text-white/80 text-sm sm:text-base',
        desc: 'text-white/75 text-sm sm:text-base max-w-2xl',
      },
    }[variant] || {
      tone: 'dark',
      overlay: 'bg-black/50',
      align: 'items-end text-left',
      crumbPill: true,
      title: 'text-3xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight text-white',
      subtitle: 'text-white/85 text-base sm:text-lg',
      desc: 'text-white/80 text-sm sm:text-base max-w-2xl',
    };

    if (showLoader) return <ImageSkeleton />;

    return (
      <section
        id={id}
        className={clsx(
          'relative w-full overflow-hidden',
          // allow rounded hero in cards, but stay full-width safe too
          '',
          heightClasses[size] || heightClasses.md,
          className
        )}
      >
        {heroImage && (
          // eslint-disable-next-line @next/next/no-img-element
          <img
            src={heroImage}
            alt={heroImageAlt || title || 'Breadcrumb header'}
            className="absolute inset-0 h-full w-full object-cover"
            loading="lazy"
          />
        )}

        {/* Decorations */}
        {Array.isArray(styles.deco) &&
          styles.deco.map((decoClass, idx) => (
            <div key={idx} className={decoClass} aria-hidden="true" />
          ))}

        <div className={clsx('absolute inset-0', styles.overlay)} />
        <div className="pointer-events-none absolute inset-0 shadow-[inset_0_-80px_120px_rgba(0,0,0,0.55)]" aria-hidden="true" />

        <div className={clsx('relative z-10 h-full w-full flex', styles.align)}>
          <div className="w-full max-w-7xl mx-auto px-5 sm:px-8 lg:px-12 py-8 sm:py-10 lg:py-14">
            <div className={clsx('relative', styles.panel, styles.frame, styles.accent)}>
              {badge && (
                <div className="mb-3 sm:mb-4">
                  <span
                    className={clsx(
                      'inline-flex items-center gap-2 rounded-full px-3 py-1 text-[11px] sm:text-xs font-semibold tracking-wide uppercase',
                      styles.badge || (styles.tone === 'dark' ? 'bg-white/10 border border-white/15 text-white' : 'bg-white/70 border border-white/70 text-slate-700')
                    )}
                  >
                    <span className={clsx('h-1.5 w-1.5 rounded-full', styles.tone === 'dark' ? 'bg-emerald-400' : 'bg-emerald-500')} />
                    {badge}
                  </span>
                </div>
              )}

              <div className={clsx(styles.minimal ? 'mb-5' : 'mb-4 sm:mb-6')}>
                {renderCrumbs({
                  tone: styles.tone,
                  pill: styles.crumbPill,
                  showHomeIcon: true,
                })}
              </div>

              {title && <h1 className={styles.title}>{title}</h1>}
              {subtitle && <p className={clsx('mt-2', styles.subtitle)}>{subtitle}</p>}
              {description && <p className={clsx('mt-4', styles.desc)}>{description}</p>}
            </div>
          </div>
        </div>
      </section>
    );
  }

  if (showLoader) return <InlineSkeleton />;

  return (
    <nav id={id} className={clsx(inlineContainerClasses[variant] || inlineContainerClasses.default, className)} aria-label="Breadcrumb">
      <ol className="flex items-center flex-wrap gap-1 sm:gap-2">
        {displayItems.map((item, index) => {
          const isLast = index === displayItems.length - 1;
          const isActive = Boolean(item?.active) || isLast;
          const label = item?.label ?? '';

          // Mobile collapse: first /  / last (only visually on mobile)
          const shouldShowEllipsis =
            collapseOnMobile &&
            Array.isArray(displayItems) &&
            displayItems.length > 3 &&
            index === 1;
          const shouldHideMiddleOnMobile =
            collapseOnMobile &&
            Array.isArray(displayItems) &&
            displayItems.length > 3 &&
            index > 0 &&
            index < displayItems.length - 1;

          if (shouldShowEllipsis) {
            return (
              <li key="ellipsis" className="flex items-center">
                <span className={clsx('inline-flex sm:hidden items-center', isInlineDark ? 'text-gray-500' : 'text-gray-400')}>
                  {variant === 'minimal' ? <SeparatorText tone={isInlineDark ? 'dark' : 'light'} /> : <ChevronIcon tone={isInlineDark ? 'dark' : 'light'} />}
                  <span className="px-1"></span>
                  {variant === 'minimal' ? <SeparatorText tone={isInlineDark ? 'dark' : 'light'} /> : <ChevronIcon tone={isInlineDark ? 'dark' : 'light'} />}
                </span>
              </li>
            );
          }

          const content = item?.href && !isActive ? (
            <a
              href={item.href}
              onClick={(e) => {
                if (onItemClick) {
                  e.preventDefault();
                  onItemClick(item, index);
                }
              }}
              className={clsx(inlineItemClasses[variant] || inlineItemClasses.default)}
            >
              {label}
            </a>
          ) : (
            <span
              className={clsx(
                inlineItemClasses[variant] || inlineItemClasses.default,
                isActive && !isInlineDark && 'font-semibold',
                isActive && isInlineDark && 'font-semibold',
                variant === 'outlined' && isActive && 'bg-gray-900 text-white px-2 py-1 rounded-full',
                variant === 'outlined' && !isActive && 'px-2 py-1 rounded-full border border-gray-200 hover:border-gray-300'
              )}
              aria-current={isActive ? 'page' : undefined}
            >
              {label}
            </span>
          );

          return (
            <li key={`${label}-${index}`} className={clsx('flex items-center', shouldHideMiddleOnMobile && 'hidden sm:flex')}>
              {index === 0 && (variant === 'icon' || variant === 'icon-dark') && (
                <span className="mr-1 inline-flex items-center">
                  <HomeIcon tone={isInlineDark ? 'dark' : 'light'} />
                </span>
              )}

              {content}

              {!isLast && !collapseOnMobile && (
                <span className="inline-flex items-center">
                  {variant === 'minimal' || variant === 'glass' ? <SeparatorText tone={isInlineDark ? 'dark' : 'light'} /> : <ChevronIcon tone={isInlineDark ? 'dark' : 'light'} />}
                </span>
              )}

              {!isLast && collapseOnMobile && (
                <span className="hidden sm:inline-flex items-center">
                  {variant === 'minimal' || variant === 'glass' ? <SeparatorText tone={isInlineDark ? 'dark' : 'light'} /> : <ChevronIcon tone={isInlineDark ? 'dark' : 'light'} />}
                </span>
              )}
            </li>
          );
        })}
      </ol>
    </nav>
  );
};


