import clsx from 'clsx';

/**
 * WorkLoadingShell Component
 * @param {Object} props - Component props
 * @param {string} [props.variant='grid'] - Variant of the skeleton to show
 * @param {number} [props.count=6] - Number of skeleton items to show
 */
export const WorkLoadingShell = ({ variant = 'grid', count = 6 }) => {
  const items = Array.from({ length: count });

  const renderGridSkeleton = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
      {items.map((_, i) => (
        <div key={i} className="flex flex-col space-y-4 animate-pulse">
          <div className="aspect-[4/3] bg-gray-200 rounded-2xl w-full" />
          <div className="h-6 bg-gray-200 rounded-full w-3/4" />
          <div className="h-4 bg-gray-200 rounded-full w-1/2" />
        </div>
      ))}
    </div>
  );

  const renderMasonrySkeleton = () => (
    <div className="columns-1 md:columns-2 lg:columns-3 gap-8 space-y-8">
      {items.map((_, i) => (
        <div key={i} className="break-inside-avoid flex flex-col space-y-4 animate-pulse">
          <div 
            className="bg-gray-200 rounded-2xl w-full" 
            style={{ height: `${200 + (i % 3) * 100}px` }} 
          />
          <div className="h-6 bg-gray-200 rounded-full w-3/4" />
          <div className="h-4 bg-gray-200 rounded-full w-1/2" />
        </div>
      ))}
    </div>
  );

  const renderBentoSkeleton = () => (
    <div className="grid grid-cols-1 md:grid-cols-4 gap-4 auto-rows-[200px]">
      {[...Array(8)].map((_, i) => (
        <div 
          key={i} 
          className={clsx(
            "bg-gray-200 rounded-3xl animate-pulse",
            i === 0 && "md:col-span-2 md:row-span-2",
            i === 1 && "md:col-span-2",
            i === 4 && "md:col-span-2",
            i === 5 && "md:row-span-2"
          )}
        />
      ))}
    </div>
  );

  const renderListSkeleton = () => (
    <div className="flex flex-col space-y-12">
      {items.map((_, i) => (
        <div key={i} className="flex flex-col md:flex-row gap-8 items-center animate-pulse">
          <div className="w-full md:w-1/2 aspect-video bg-gray-200 rounded-2xl" />
          <div className="w-full md:w-1/2 space-y-4">
            <div className="h-4 bg-gray-200 rounded-full w-24" />
            <div className="h-10 bg-gray-200 rounded-full w-full" />
            <div className="h-20 bg-gray-200 rounded-2xl w-full" />
            <div className="h-10 bg-gray-200 rounded-full w-32" />
          </div>
        </div>
      ))}
    </div>
  );

  return (
    <div className="w-full max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      {variant === 'grid' || variant === 'modern' || variant === 'minimal' || variant === 'interactive' ? renderGridSkeleton() : null}
      {variant === 'masonry' ? renderMasonrySkeleton() : null}
      {variant === 'bento' ? renderBentoSkeleton() : null}
      {variant === 'split' || variant === 'list' || variant === 'timeline' ? renderListSkeleton() : null}
      {variant === 'carousel' && (
        <div className="w-full aspect-video bg-gray-200 rounded-3xl animate-pulse" />
      )}
    </div>
  );
};

