import React from 'react';
import { PlansStream as PlansStreamBase } from './PlansStream';

export const PlansStream = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PlansStreamBase, props)
  );
};
