import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { ReachUsLoadingShell } from '../ReachUsLoadingShell';
import '../../styles/contact.css';

/**
t  Component - Professional contact sections inspired by Modern UI
 * @param {Object} props - Component props
 * @param {Object} props.data - Contact section data
 * @param {string} [props.data.variant='infoLeft'] - Style variant: 'infoLeft' | 'infoRight' | 'stacked' | 'centered' | 'card'
 * @param {string} [props.id] - Optional section id (falls back to data.id)
 * @param {boolean} [props.loading=false] - Loading state for skeleton
 * @param {string} [props.className] - Additional CSS classes
 */
export const ReachUsNeo = ({
  data = {},
  className,
  loading = false,
  id
}) => {
  const [timedOut, setTimedOut] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });

  const variant = data.variant || 'infoLeft';

  // Reset timeout when data changes to ensure dynamic content works
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setTimedOut(false);
    }
  }, [loading, data]); // Added data to dependencies to reset on data changes

  const shouldShowSkeleton = loading && !timedOut;

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
    // Add logic here
  };

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <ReachUsLoadingShell data={{ variant: 'classic' }} className={className} />;
  }

  const renderVariant = () => {
    switch (variant) {
      case 'infoLeft':
        return (
          <div className="max-w-7xl mx-auto px-4 py-24 lg:py-32">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-16">
              {/* Contact Info */}
              <div className="space-y-8">
                {data.title && (
                  <h2 className="text-4xl font-bold text-black mb-6">
                    {data.title}
                  </h2>
                )}
                {(data.subtitle || data.description) && (
                  <p className="text-gray-600 text-lg mb-8">
                    {data.subtitle || data.description}
                  </p>
                )}

                {/* Contact Details */}
                {data.info && data.info.length > 0 && (
                  <div className="space-y-6">
                    {data.info.map((item, idx) => (
                      <div key={idx} className="flex items-start space-x-4">
                        <div className="w-12 h-12 bg-blue-50 rounded-lg flex items-center justify-center flex-shrink-0">
                          {item.icon}
                        </div>
                        <div>
                          {item.title && (
                            <h3 className="font-semibold text-gray-900 mb-1">{item.title}</h3>
                          )}
                          <p className="text-gray-600">{item.value}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>

              {/* Contact Form */}
              {data.form && (
                <div className="bg-white border border-gray-200 rounded-xl p-8 shadow-sm">
                  <form onSubmit={handleSubmit} className="space-y-6">
                    {(data.form.fields || [
                      { name: 'email', type: 'email', label: 'Email', required: true },
                      { name: 'name', type: 'text', label: 'Name' },
                      { name: 'message', type: 'textarea', label: 'Your message', required: true, rows: 4 }
                    ]).map((field, idx) => (
                      <div key={field.name} className="space-y-2">
                        <label className="block text-sm font-medium text-gray-700">
                          {field.label} {field.required && <span className="text-red-500">*</span>}
                        </label>
                        {field.type === 'textarea' ? (
                          <textarea
                            name={field.name}
                            rows={field.rows || 3}
                            required={field.required}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        ) : (
                          <input
                            type={field.type || 'text'}
                            name={field.name}
                            required={field.required}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        )}
                      </div>
                    ))}

                    {data.form?.buttonText && (
                      <button
                        type="submit"
                        className="w-full bg-primary-600 text-white py-3 px-6 rounded-lg font-medium hover:bg-primary-700 transition-colors"
                      >
                        {data.form.buttonText}
                      </button>
                    )}
                  </form>
                </div>
              )}
            </div>
          </div>
        );

      case 'infoRight':
        return (
          <div className="max-w-7xl mx-auto px-4 py-24 lg:py-32">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-16">
              {/* Contact Form */}
              {data.form && (
                <div className="bg-white border border-gray-200 rounded-xl p-8 shadow-sm">
                  <form onSubmit={handleSubmit} className="space-y-6">
                    {(data.form.fields || [
                      { name: 'name', type: 'text', label: 'Your name' },
                      { name: 'email', type: 'email', label: 'Your email', required: true },
                      { name: 'subject', type: 'text', label: 'Subject', required: true },
                      { name: 'message', type: 'textarea', label: 'Your message', rows: 4 }
                    ]).map((field, idx) => (
                      <div key={field.name} className="space-y-2">
                        <label className="block text-sm font-medium text-gray-700">
                          {field.label} {field.required && <span className="text-red-500">*</span>}
                        </label>
                        {field.type === 'textarea' ? (
                          <textarea
                            name={field.name}
                            rows={field.rows || 3}
                            required={field.required}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        ) : (
                          <input
                            type={field.type || 'text'}
                            name={field.name}
                            required={field.required}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        )}
                      </div>
                    ))}

                    {data.form?.buttonText && (
                      <button
                        type="submit"
                        className="w-full bg-primary-600 text-white py-3 px-6 rounded-lg font-medium hover:bg-primary-700 transition-colors"
                      > 
                        {data.form.buttonText}
                      </button>
                    )}
                  </form>
                </div>
              )}

              {/* Contact Info */}
              <div className="space-y-8">
                {data.title && (
                  <h2 className="text-4xl font-bold text-black mb-6">
                    {data.title}
                  </h2>
                )}
                {(data.subtitle || data.description) && (
                  <p className="text-gray-600 text-lg mb-8">
                    {data.subtitle || data.description}
                  </p>
                )}

                {/* Contact Details */}
                {data.info && data.info.length > 0 && (
                  <div className="space-y-6">
                    {data.info.map((item, idx) => (
                      <div key={idx} className="flex items-start space-x-4">
                        <div className="w-12 h-12 bg-green-50 rounded-lg flex items-center justify-center flex-shrink-0">
                          {item.icon}
                        </div>
                        <div>
                          {item.title && (
                            <h3 className="font-semibold text-gray-900 mb-1">{item.title}</h3>
                          )}
                          <p className="text-gray-600">{item.value}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        );

      case 'stacked':
        return (
          <div className="max-w-4xl mx-auto px-4 py-24 lg:py-32">
            {/* Contact Info Header */}
            {data.info && data.info.length > 0 && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mb-16">
                {data.info.map((item, idx) => (
                  <div key={idx} className="text-center">
                      <div className="w-16 h-16 bg-blue-50 rounded-full flex items-center justify-center mx-auto mb-4">
                        {item.icon}
                      </div>
                    {item.title && (
                      <h3 className="font-semibold text-gray-900 mb-2">{item.title}</h3>
                    )}
                    <p className="text-gray-600 text-sm">{item.value}</p>
                  </div>
                ))}
              </div>
            )}

            {/* Contact Form */}
            {data.form && (
              <div className="bg-white border border-gray-200 rounded-xl p-8 shadow-sm max-w-2xl mx-auto">
                {data.title && (
                  <div className="text-center mb-8">
                    <h2 className="text-3xl font-bold text-black mb-2">
                      {data.title}
                    </h2>
                    {(data.subtitle || data.description) && (
                      <p className="text-gray-600">
                        {data.subtitle || data.description}
                      </p>
                    )}
                  </div>
                )}

                <form onSubmit={handleSubmit} className="space-y-6">
                  {(data.form.fields || [
                    { name: 'name', type: 'text', label: 'Name' },
                    { name: 'email', type: 'email', label: 'Email' },
                    { name: 'subject', type: 'text', label: 'Subject' },
                    { name: 'message', type: 'textarea', label: 'Message', rows: 4 }
                  ]).map((field, idx) => (
                    <div key={field.name} className="space-y-2">
                      <label className="block text-sm font-medium text-gray-700">
                        {field.label}
                      </label>
                      {field.type === 'textarea' ? (
                        <textarea
                          name={field.name}
                          rows={field.rows || 3}
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                          onChange={handleInputChange}
                        />
                      ) : (
                        <input
                          type={field.type || 'text'}
                          name={field.name}
                          className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                          onChange={handleInputChange}
                        />
                      )}
                    </div>
                  ))}

                  {data.form?.buttonText && (
                    <button
                      type="submit"
                      className="w-full bg-primary-600 text-white py-3 px-6 rounded-lg font-medium hover:bg-primary-700 transition-colors"
                    >
                      {data.form.buttonText}
                    </button>
                  )}
                </form>
              </div>
            )}
          </div>
        );

      case 'centered':
        return (
          <div className="max-w-6xl mx-auto px-4 py-24 lg:py-32">
            <div className="text-center mb-16">
              {data.title && (
                <h2 className="text-4xl font-bold text-black mb-4">
                  {data.title}
                </h2>
              )}
              {(data.subtitle || data.description) && (
                <p className="text-gray-600 text-lg max-w-2xl mx-auto">
                  {data.subtitle || data.description}
                </p>
              )}
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
              {/* Contact Info */}
              {data.info && data.info.length > 0 && (
                <div className="space-y-6">
                  {data.info.map((item, idx) => (
                    <div key={idx} className="text-center">
                      <div className="w-14 h-14 bg-purple-50 rounded-full flex items-center justify-center mx-auto mb-4">
                        {item.icon}
                      </div>
                      {item.title && (
                        <h3 className="font-semibold text-gray-900 mb-2">{item.title}</h3>
                      )}
                      <p className="text-gray-600">{item.value}</p>
                    </div>
                  ))}
                </div>
              )}

              {/* Contact Form */}
              {data.form && (
                <div className="lg:col-span-2 bg-white border border-gray-200 rounded-xl p-8 shadow-sm">
                  <form onSubmit={handleSubmit} className="space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      {(data.form.fields || [
                        { name: 'name', type: 'text', label: 'Name' },
                        { name: 'email', type: 'email', label: 'Email' }
                      ]).slice(0, 2).map((field, idx) => (
                        <div key={field.name} className="space-y-2">
                          <label className="block text-sm font-medium text-gray-700">
                            {field.label}
                          </label>
                          <input
                            type={field.type || 'text'}
                            name={field.name}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        </div>
                      ))}
                    </div>

                    {(data.form.fields || [
                      { name: 'subject', type: 'text', label: 'Subject' },
                      { name: 'message', type: 'textarea', label: 'Message', rows: 4 }
                    ]).slice(2).map((field, idx) => (
                      <div key={field.name} className="space-y-2">
                        <label className="block text-sm font-medium text-gray-700">
                          {field.label}
                        </label>
                        {field.type === 'textarea' ? (
                          <textarea
                            name={field.name}
                            rows={field.rows || 3}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        ) : (
                          <input
                            type={field.type || 'text'}
                            name={field.name}
                            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                            onChange={handleInputChange}
                          />
                        )}
                      </div>
                    ))}

                    {data.form?.buttonText && (
                      <button
                        type="submit"
                        className="w-full bg-primary-600 text-white py-3 px-6 rounded-lg font-medium hover:bg-primary-700 transition-colors"
                      >
                        {data.form.buttonText}
                      </button>
                    )}
                  </form>
                </div>
              )}
            </div>
          </div>
        );

      case 'card':
        return (
          <div className="max-w-4xl mx-auto px-4 py-24 lg:py-32">
            <div className="bg-white border border-gray-200 rounded-2xl shadow-xl overflow-hidden">
              <div className="grid grid-cols-1 lg:grid-cols-2">
                {/* Contact Info */}
                <div className="bg-gray-50 p-8 lg:p-12">
                  {data.title && (
                    <h2 className="text-3xl font-bold text-black mb-6">
                      {data.title}
                    </h2>
                  )}
                  {(data.subtitle || data.description) && (
                    <p className="text-gray-600 mb-8">
                      {data.subtitle || data.description}
                    </p>
                  )}

                  {data.info && data.info.length > 0 && (
                    <div className="space-y-6">
                      {data.info.map((item, idx) => (
                        <div key={idx} className="flex items-center space-x-4">
                        <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center flex-shrink-0">
                          {item.icon}
                        </div>
                          <div>
                            {item.title && (
                              <p className="font-medium text-gray-900">{item.title}</p>
                            )}
                            <p className="text-gray-600 text-sm">{item.value}</p>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>

                {/* Contact Form */}
                {data.form && (
                  <div className="p-8 lg:p-12">
                    <form onSubmit={handleSubmit} className="space-y-6">
                      {(data.form.fields || [
                        { name: 'name', type: 'text', label: 'Name' },
                        { name: 'email', type: 'email', label: 'Email' },
                        { name: 'subject', type: 'text', label: 'Subject' },
                        { name: 'message', type: 'textarea', label: 'Message', rows: 4 }
                      ]).map((field, idx) => (
                        <div key={field.name} className="space-y-2">
                          <label className="block text-sm font-medium text-gray-700">
                            {field.label}
                          </label>
                          {field.type === 'textarea' ? (
                            <textarea
                              name={field.name}
                              rows={field.rows || 3}
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                              onChange={handleInputChange}
                            />
                          ) : (
                            <input
                              type={field.type || 'text'}
                              name={field.name}
                              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                              onChange={handleInputChange}
                            />
                          )}
                        </div>
                      ))}

                      {data.form?.buttonText && (
                        <button
                          type="submit"
                          className="w-full bg-primary-600 text-white py-3 px-6 rounded-lg font-medium hover:bg-primary-700 transition-colors"
                        >
                          {data.form.buttonText}
                        </button>
                      )}
                    </form>
                  </div>
                )}
              </div>
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <section
      id={id || data?.id}
      className={clsx(
      "contact-modern-section",
      className
    )}
    >
      {renderVariant()}
    </section>
  );
};
