import React from 'react';
import { QtyPicker as QtyPickerBase } from './QtyPicker';

export const QtyPicker = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(QtyPickerBase, props)
  );
};
