import React from 'react';
import { CrewPanel as CrewPanelBase } from './CrewPanel';

export const CrewPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CrewPanelBase, props)
  );
};
