import React from 'react';
import { PromoBadge as PromoBadgeBase } from './PromoBadge';

export const PromoBadge = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PromoBadgeBase, props)
  );
};
