import React from 'react';
import { FilterRail as FilterRailBase } from './FilterRail';

export const FilterRail = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(FilterRailBase, props)
  );
};
