import { useEffect, useMemo, useRef, useState } from 'react';
import clsx from 'clsx';
import '../../styles/team.css';

const Icon = ({ name }) => {
  // Minimal inline icons (no extra deps)
  const common = {
    width: 18,
    height: 18,
    viewBox: '0 0 24 24',
    fill: 'currentColor',
    'aria-hidden': 'true',
    focusable: 'false',
  };

  if (name === 'linkedin') {
    return (
      <svg {...common}>
        <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.476-.9 1.637-1.852 3.368-1.852 3.6 0 4.268 2.369 4.268 5.455v6.288zM5.337 7.433a2.067 2.067 0 1 1 0-4.134 2.067 2.067 0 0 1 0 4.134zM6.815 20.452H3.86V9h2.955v11.452z" />
      </svg>
    );
  }

  if (name === 'twitter') {
    return (
      <svg {...common}>
        <path d="M22 5.92c-.75.33-1.56.55-2.41.65a4.2 4.2 0 0 0 1.84-2.32 8.3 8.3 0 0 1-2.66 1.02 4.14 4.14 0 0 0-7.06 3.77A11.75 11.75 0 0 1 3.15 4.9a4.13 4.13 0 0 0 1.28 5.53c-.65-.02-1.26-.2-1.8-.5v.05c0 2 1.45 3.67 3.37 4.05-.35.1-.73.15-1.12.15-.27 0-.54-.03-.8-.08.54 1.72 2.1 2.97 3.95 3a8.3 8.3 0 0 1-5.14 1.77c-.33 0-.66-.02-.98-.06A11.7 11.7 0 0 0 8.29 21c7.55 0 11.68-6.33 11.68-11.82 0-.18 0-.36-.01-.54A8.5 8.5 0 0 0 22 5.92z" />
      </svg>
    );
  }

  if (name === 'instagram') {
    return (
      <svg {...common}>
        <path d="M7 2h10a5 5 0 0 1 5 5v10a5 5 0 0 1-5 5H7a5 5 0 0 1-5-5V7a5 5 0 0 1 5-5zm10 2H7a3 3 0 0 0-3 3v10a3 3 0 0 0 3 3h10a3 3 0 0 0 3-3V7a3 3 0 0 0-3-3zm-5 3.5A5.5 5.5 0 1 1 6.5 13 5.51 5.51 0 0 1 12 7.5zm0 2A3.5 3.5 0 1 0 15.5 13 3.5 3.5 0 0 0 12 9.5zM18 6.5a1 1 0 1 1-1 1 1 1 0 0 1 1-1z" />
      </svg>
    );
  }

  if (name === 'github') {
    return (
      <svg {...common}>
        <path d="M12 2a10 10 0 0 0-3.16 19.49c.5.09.68-.22.68-.48v-1.7c-2.77.61-3.35-1.17-3.35-1.17-.45-1.16-1.1-1.46-1.1-1.46-.9-.63.07-.62.07-.62 1 .07 1.53 1.03 1.53 1.03.89 1.54 2.34 1.09 2.9.83.09-.66.35-1.09.64-1.34-2.21-.25-4.54-1.12-4.54-4.98 0-1.1.39-2 1.03-2.71-.1-.26-.45-1.29.1-2.68 0 0 .84-.27 2.75 1.03A9.4 9.4 0 0 1 12 6.8c.85 0 1.71.12 2.51.35 1.9-1.3 2.74-1.03 2.74-1.03.55 1.39.2 2.42.1 2.68.64.71 1.03 1.61 1.03 2.71 0 3.87-2.34 4.72-4.57 4.97.36.31.69.92.69 1.86v2.75c0 .27.18.58.69.48A10 10 0 0 0 12 2z" />
      </svg>
    );
  }

  if (name === 'mail') {
    return (
      <svg {...common}>
        <path d="M20 4H4a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V6a2 2 0 0 0-2-2zm0 4-8 5L4 8V6l8 5 8-5v2z" />
      </svg>
    );
  }

  return null;
};

const normalizeVariant = (v) => {
  if (v == null) return 1;
  if (typeof v === 'number') return v;
  const s = String(v).toLowerCase().trim();
  if (s.startsWith('variant')) return Number(s.replace('variant', '')) || 1;
  return Number(s) || 1;
};

const normalizeTheme = (t) => {
  if (t == null) return 'light';
  const s = String(t).toLowerCase().trim();
  return s === 'dark' ? 'dark' : 'light';
};

const truncateText = (value, maxLength, ellipsis = '...') => {
  const text = String(value ?? '');
  const max = Number(maxLength);
  if (!Number.isFinite(max) || max <= 0) return text;
  if (text.length <= max) return text;
  const cut = Math.max(0, max - ellipsis.length);
  return `${text.slice(0, cut).trimEnd()}${ellipsis}`;
};

/**
 * TeamSection Component (6 variants)
 *
 * Requirements: same base design system; only layout behavior changes per variant.
 *
 * @param {Object} props
 * @param {Object} props.data
 * @param {string} [props.data.subtitle]
 * @param {string} [props.data.title]
 * @param {string} [props.data.description]
 * @param {Array}  [props.data.members]
 * @param {number|string} [props.data.variant=1] Variant 1..6
 * @param {string} [props.data.featuredName] For Variant 6 featured card highlight
 * @param {"light"|"dark"} [props.data.theme="light"] Theme (light | dark)
 * @param {number} [props.data.memberDescriptionMaxLength=120] Max length for member bio/description text (0/undefined disables)
 * @param {string} [props.data.memberDescriptionEllipsis="..."] Ellipsis string appended when truncated
 * @param {string} [props.data.className]
 * @param {string} [props.className]
 * @param {string} [props.id]
 * @param {"light"|"dark"} [props.theme] Theme override (preferred over data.theme)
 */
export const TeamSection = ({ data, className, id, theme: themeProp }) => {
  if (!data || typeof data !== 'object') {
    console.error('TeamSection: data prop is required and must be an object');
    return null;
  }

  const {
    subtitle,
    title = 'Team',
    description,
    members = [],
    variant: variantRaw = 1,
    featuredName,
    theme: themeFromData,
    memberDescriptionMaxLength = 120,
    memberDescriptionEllipsis = '...',
    className: dataClassName,
  } = data;

  const variant = normalizeVariant(variantRaw);
  const theme = normalizeTheme(themeProp ?? themeFromData);

  const sectionClass = clsx(
    'ls-team',
    `ls-team--v${variant}`,
    `ls-team--theme-${theme}`,
    className,
    dataClassName
  );

  const header = (
    <div className="ls-team__header">
      {subtitle && <div className="ls-team__subtitle">{subtitle}</div>}
      {title && <h2 className="ls-team__title">{title}</h2>}
      {description && <p className="ls-team__desc">{description}</p>}
    </div>
  );

  const renderSocials = (socials, ariaLabel = 'Social links') => {
    if (!socials || typeof socials !== 'object') return null;

    const entries = Object.entries(socials).filter(([, href]) => Boolean(href));
    if (entries.length === 0) return null;

    return (
      <div className="ls-team__socials" aria-label={ariaLabel}>
        {entries.map(([key, href]) => (
          <a
            key={key}
            href={href}
            className="ls-team__socialBtn"
            aria-label={key}
            target={href.startsWith('#') ? undefined : '_blank'}
            rel={href.startsWith('#') ? undefined : 'noreferrer'}
          >
            <Icon name={key} />
          </a>
        ))}
      </div>
    );
  };

  const getMemberBioText = (m) => {
    const raw = m?.bio || m?.description || '';
    return truncateText(raw, memberDescriptionMaxLength, memberDescriptionEllipsis);
  };

  const renderCard = (m, opts = {}) => {
    const {
      imageStyle = 'rect', // rect | circle
      showBio = true,
      showSocials = true,
      featured = false,
      grayImage = false,
    } = opts;

    return (
      <article className={clsx('ls-team__card', featured && 'is-featured')}>
        <div className="ls-team__imgWrap">
          {m.avatar ? (
            <img
              className="ls-team__img"
              src={m.avatar}
              alt={m.name || 'Team member'}
              style={grayImage ? { filter: 'grayscale(1)' } : undefined}
              loading="lazy"
            />
          ) : (
            <div className="ls-team__img" aria-hidden="true" />
          )}
        </div>

        <div className="ls-team__body">
          {m.name && <h3 className="ls-team__name">{m.name}</h3>}
          {m.role && <p className="ls-team__role">{m.role}</p>}
          {showBio && (m.bio || m.description) && (
            <p className="ls-team__bio">{getMemberBioText(m)}</p>
          )}
          {showSocials && renderSocials(m.socials)}
        </div>
      </article>
    );
  };

  // Variant 4: carousel layout (snap scrolling + buttons + dots)
  const Carousel = () => {
    const trackRef = useRef(null);
    const [active, setActive] = useState(0);

    // Slides: use 4-up groups on desktop-ish feel, but keep snap per card.
    const slides = members;

    const maxIndex = Math.max(0, slides.length - 1);

    const scrollToIndex = (idx) => {
      const el = trackRef.current;
      if (!el) return;
      const child = el.children?.[idx];
      if (!child) return;
      child.scrollIntoView({ behavior: 'smooth', inline: 'start', block: 'nearest' });
      setActive(idx);
    };

    useEffect(() => {
      const el = trackRef.current;
      if (!el) return;
      const onScroll = () => {
        const children = Array.from(el.children);
        const left = el.scrollLeft;
        let bestIdx = 0;
        let bestDist = Infinity;
        children.forEach((c, idx) => {
          const dist = Math.abs(c.offsetLeft - left);
          if (dist < bestDist) {
            bestDist = dist;
            bestIdx = idx;
          }
        });
        setActive(bestIdx);
      };
      el.addEventListener('scroll', onScroll, { passive: true });
      return () => el.removeEventListener('scroll', onScroll);
    }, []);

    const dotCount = Math.min(6, slides.length);
    const dotIndexes = useMemo(() => {
      if (slides.length <= dotCount) return slides.map((_, i) => i);
      // pick evenly spaced dots
      const step = (slides.length - 1) / (dotCount - 1);
      return Array.from({ length: dotCount }, (_, i) => Math.round(i * step));
    }, [slides.length, dotCount]);

    return (
      <div className="ls-team__carousel">
        <button
          type="button"
          className="ls-team__navBtn ls-team__navBtn--prev"
          aria-label="Previous"
          onClick={() => scrollToIndex(Math.max(0, active - 1))}
          disabled={active <= 0}
        >
          <svg width="18" height="18" viewBox="0 0 24 24" fill="none" aria-hidden="true">
            <path d="M15 19l-7-7 7-7" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" />
          </svg>
        </button>

        <div className="ls-team__track" ref={trackRef}>
          {slides.map((m, idx) => (
            <div className="ls-team__slide" key={`${m.name || idx}-${idx}`}>
              {renderCard(m, { showSocials: false, showBio: true })}
            </div>
          ))}
        </div>

        <button
          type="button"
          className="ls-team__navBtn ls-team__navBtn--next"
          aria-label="Next"
          onClick={() => scrollToIndex(Math.min(maxIndex, active + 1))}
          disabled={active >= maxIndex}
        >
          <svg width="18" height="18" viewBox="0 0 24 24" fill="none" aria-hidden="true">
            <path d="M9 5l7 7-7 7" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" />
          </svg>
        </button>

        <div className="ls-team__dots" aria-label="Carousel pagination">
          {dotIndexes.map((idx) => (
            <button
              key={idx}
              type="button"
              className={clsx('ls-team__dot', idx === active && 'is-active')}
              aria-label={`Go to slide ${idx + 1}`}
              onClick={() => scrollToIndex(idx)}
            />
          ))}
        </div>
      </div>
    );
  };

  const content = (() => {
    // Keep the header visual consistent; variants adjust layout below.
    if (variant === 4) {
      return (
        <>
          {header}
          <Carousel />
        </>
      );
    }

    if (variant === 1) {
      return (
        <>
          {header}
          <div className="ls-team__grid">
            {members.map((m, idx) => (
              <div key={`${m.name || idx}-${idx}`}>
                {renderCard(m, {
                  imageStyle: 'circle',
                  showSocials: false,
                  showBio: true,
                })}
              </div>
            ))}
          </div>
        </>
      );
    }

    if (variant === 2) {
      return (
        <>
          {header}
          <div className="ls-team__grid">
            {members.map((m, idx) => (
              <div key={`${m.name || idx}-${idx}`}>
                {renderCard(m, {
                  showSocials: true,
                  showBio: true,
                  grayImage: true,
                })}
              </div>
            ))}
          </div>
        </>
      );
    }

    if (variant === 3) {
      return (
        <>
          {header}
          <div className="ls-team__grid">
            {members.map((m, idx) => (
              <div key={`${m.name || idx}-${idx}`}>
                {renderCard(m, { showSocials: false, showBio: false })}
              </div>
            ))}
          </div>
        </>
      );
    }

    if (variant === 5) {
      return (
        <>
          {header}
          <div className="ls-team__grid">
            {members.map((m, idx) => (
              <div key={`${m.name || idx}-${idx}`}>
                {renderCard(m, { showSocials: true, showBio: true })}
              </div>
            ))}
          </div>
        </>
      );
    }

    // Variant 6
    const featured = featuredName || members?.[1]?.name;
    return (
      <>
        {header}
        <div className="ls-team__grid">
          {members.map((m, idx) => (
            <div key={`${m.name || idx}-${idx}`}>
              <article className={clsx('ls-team__card', m.name === featured && 'is-featured')}>
                <div className="ls-team__imgWrap">
                  {m.avatar ? (
                    <img className="ls-team__img" src={m.avatar} alt={m.name || 'Team member'} loading="lazy" />
                  ) : (
                    <div className="ls-team__img" aria-hidden="true" />
                  )}
                </div>

                <div className="ls-team__body">
                  {m.name && <h3 className="ls-team__name">{m.name}</h3>}
                  {m.role && <p className="ls-team__role">{m.role}</p>}

                  {(m.meta || m.description || m.bio) && (
                    <div className="ls-team__meta">
                      {m.meta ? (
                        <>
                          {Array.isArray(m.meta) ? (
                            m.meta.map((line, i) => <div key={i}>{line}</div>)
                          ) : (
                            <div>{m.meta}</div>
                          )}
                        </>
                      ) : (
                        <div>{getMemberBioText(m)}</div>
                      )}
                    </div>
                  )}

                  <div className="ls-team__divider" />

                  {renderSocials(m.socials, 'Contact links')}
                </div>
              </article>
            </div>
          ))}
        </div>
      </>
    );
  })();

  return (
    <section id={id} className={sectionClass}>
      <div className="ls-team__container">{content}</div>
    </section>
  );
};


