import React from 'react';
import { ArticleDetail as ArticleDetailBase } from './ArticleDetail';

export const ArticleDetail = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ArticleDetailBase, props)
  );
};
