import React from 'react';
import { OrderRecapPanel as OrderRecapPanelBase } from './OrderRecapPanel';

export const OrderRecapPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(OrderRecapPanelBase, props)
  );
};
