import React from 'react';
import { HelpPulse as HelpPulseBase } from './HelpPulse';

export const HelpPulse = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HelpPulseBase, props)
  );
};
