import React from 'react';
import { ProgressDots as ProgressDotsBase } from './ProgressDots';

export const ProgressDots = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ProgressDotsBase, props)
  );
};
