import React from 'react';
import { ItemPageFrame as ItemPageFrameBase } from './ItemPageFrame';

export const ItemPageFrame = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemPageFrameBase, props)
  );
};
