import React from 'react';
import { OriginPanel as OriginPanelBase } from './OriginPanel';
import { OriginLoadingShell as OriginLoadingShellBase } from './OriginLoadingShell';

export const OriginPanel = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(OriginPanelBase, props)
  );
};
export const OriginLoadingShell = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(OriginLoadingShellBase, props)
  );
};
