import React from 'react';
import { BasketSideDrawer as BasketSideDrawerBase } from './BasketSideDrawer';

export const BasketSideDrawer = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BasketSideDrawerBase, props)
  );
};
