import React from 'react';
import { ReachUsNeo as ReachUsNeoBase } from './ReachUsNeo';

export const ReachUsNeo = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ReachUsNeoBase, props)
  );
};
