import React from 'react';
import { CatalogView as CatalogViewBase } from './CatalogView';

export const CatalogView = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CatalogViewBase, props)
  );
};
