import { useState, useEffect } from 'react';
import clsx from 'clsx';
import { MosaicCard } from './MosaicCard';
import '../../styles/grid-sections.css';

/**
 * UniversalGrid Component - Structural grid layouts inspired by Mantine UI
 * @param {Object} props - Component props
 * @param {Object} props.data - Grid data and configuration
 * @param {Array} props.data.items - Array of card data objects
 * @param {string} [props.data.title] - Section title
 * @param {string} [props.data.subtitle] - Section subtitle
 * @param {string} [props.data.variant='leading-item'] - 'leading-item' | 'asymmetrical' | 'feature-top' | 'staggered' | 'compact-vertical'
 * @param {string} [props.data.theme='light'] - Section theme: 'light' | 'dark'
 * @param {boolean} [props.loader=false] - Show loading skeletons
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - Component ID
 */
export const MosaicGrid = ({ data, className, id, loader = false }) => {
  if (!data || !data.items) {
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
    setShowLoader(false);
  }, [loader]);

  const {
    items = [],
    title,
    subtitle,
    variant = 'leading-item',
    theme = 'light',
    className: dataClassName,
  } = data;

  const sectionClasses = clsx(
    'universal-grid-section',
    `universal-grid-theme-${theme}`,
    className,
    dataClassName
  );

  const gridContainerClasses = clsx(
    `grid-${variant}`,
    'container mx-auto px-4'
  );

  return (
    <section className={sectionClasses} id={id}>
      <div className="container mx-auto px-4">
        {(title || subtitle) && (
          <div className="universal-grid-header">
            {subtitle && <span className="universal-grid-subtitle">{subtitle}</span>}
            {title && <h2 className="universal-grid-title">{title}</h2>}
          </div>
        )}

        <div className={`grid-${variant}`}>
          {showLoader ? (
            Array.from({ length: 6 }).map((_, index) => (
              <div key={`skeleton-${index}`} className={clsx(variant === 'leading-item' && index === 0 && 'large-card')}>
                <MosaicCard loader={true} data={{ variant }} />
              </div>
            ))
          ) : (
            items.map((item, index) => (
              <div 
                key={item.id || index}
                className={clsx(
                  variant === 'leading-item' && index === 0 && 'large-card'
                )}
              >
                <MosaicCard
                  data={{ ...item, variant, theme }}
                />
              </div>
            ))
          )}
        </div>
      </div>
    </section>
  );
};
