import React from 'react';
import { ItemPageNeo as ItemPageNeoBase } from './ItemPageNeo';

export const ItemPageNeo = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemPageNeoBase, props)
  );
};
