import React from 'react';
import { ItemShowcaseBare as ItemShowcaseBareBase } from './ItemShowcaseBare';

export const ItemShowcaseBare = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ItemShowcaseBareBase, props)
  );
};
