import React from 'react';
import { MotionRail as MotionRailBase } from './MotionRail';

export const MotionRail = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(MotionRailBase, props)
  );
};
