import React from 'react';
import { CommentThread as CommentThreadBase } from './CommentThread';

export const CommentThread = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(CommentThreadBase, props)
  );
};
