import React from 'react';
import { PurchaseFlowElite as PurchaseFlowEliteBase } from './PurchaseFlowElite';

export const PurchaseFlowElite = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseFlowEliteBase, props)
  );
};
