import React from 'react';
import { MediaGallery as MediaGalleryBase } from './MediaGallery';

export const MediaGallery = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(MediaGalleryBase, props)
  );
};
