import React from 'react';
import { HelpShowcase as HelpShowcaseBase } from './HelpShowcase';

export const HelpShowcase = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(HelpShowcaseBase, props)
  );
};
