import React from 'react';
import { LostPage as LostPageBase } from './LostPage';

export const LostPage = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(LostPageBase, props)
  );
};
