import React from 'react';
import { BradCrump as BradCrumpBase } from './BradCrump';

export const BradCrump = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BradCrumpBase, props)
  );
};
