import React from 'react';
import { PlansBoard as PlansBoardBase } from './PlansBoard';

export const PlansBoard = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PlansBoardBase, props)
  );
};
