import { useState, useEffect } from 'react';
import clsx from 'clsx';
import { WorkLoadingShell } from './WorkLoadingShell';

/**
 * Portfolio Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Portfolio section configuration data
 * @param {string} props.data.title - Section title
 * @param {string} [props.data.subtitle] - Section subtitle
 * @param {Array} props.data.items - Portfolio items
 * @param {string} [props.data.variant='grid'] - Layout variant
 * @param {string} [props.id] - Optional section id (falls back to data.id)
 * @param {string} [props.className] - Additional CSS classes
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const WorkGallery = ({ data, className, loader = false, id }) => {
  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
  }, [loader]);

  if (!data || !data.items) {
    return null;
  }

  const {
    title,
    subtitle,
    items,
    variant = 'grid',
    className: dataClassName,
  } = data;

  if (showLoader) {
    return <WorkLoadingShell variant={variant} count={items.length} />;
  }

  const SectionHeader = () => (
    (title || subtitle) && (
      <div className="mb-12 text-center">
        {subtitle && (
          <span className="text-primary-600 font-semibold uppercase tracking-widest text-sm mb-2 block">
            {subtitle}
          </span>
        )}
        {title && (
          <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold text-gray-900">
            {title}
          </h2>
        )}
      </div>
    )
  );

  // 1. Grid Classic
  const GridClassic = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
      {items.map((item, index) => (
        <div key={index} className="group cursor-pointer">
          <div className="relative overflow-hidden rounded-2xl aspect-[4/3] bg-gray-100">
            <img
              src={item.image}
              alt={item.title}
              className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-110"
            />
            <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors duration-300" />
          </div>
          <div className="mt-4">
            <span className="text-primary-600 text-sm font-medium">{item.category}</span>
            <h3 className="text-xl font-bold text-gray-900 mt-1">{item.title}</h3>
          </div>
        </div>
      ))}
    </div>
  );

  // 2. Masonry
  const MasonryLayout = () => (
    <div className="columns-1 md:columns-2 lg:columns-3 gap-8 space-y-8">
      {items.map((item, index) => (
        <div key={index} className="break-inside-avoid group cursor-pointer">
          <div className="relative overflow-hidden rounded-2xl bg-gray-100">
            <img
              src={item.image}
              alt={item.title}
              className="w-full h-auto object-cover transition-transform duration-500 group-hover:scale-105"
            />
            <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex flex-col justify-end p-6">
              <span className="text-primary-400 text-sm font-medium">{item.category}</span>
              <h3 className="text-white text-xl font-bold mt-1">{item.title}</h3>
            </div>
          </div>
        </div>
      ))}
    </div>
  );

  // 3. Modern Cards
  const ModernCards = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-10">
      {items.map((item, index) => (
        <div key={index} className="bg-white rounded-3xl overflow-hidden shadow-xl shadow-gray-200/50 group border border-gray-100 hover:border-primary-500/30 transition-all duration-300 hover:-translate-y-2">
          <div className="relative aspect-video overflow-hidden">
            <img
              src={item.image}
              alt={item.title}
              className="w-full h-full object-cover"
            />
          </div>
          <div className="p-8">
            <div className="flex items-center gap-2 mb-3">
              <span className="px-3 py-1 bg-primary-50 text-primary-600 text-xs font-bold rounded-full uppercase tracking-wider">
                {item.category}
              </span>
            </div>
            <h3 className="text-2xl font-bold text-gray-900 mb-2">{item.title}</h3>
            <p className="text-gray-500 line-clamp-2">{item.description}</p>
            <div className="mt-6 flex items-center text-primary-600 font-bold group-hover:gap-2 transition-all">
              View Project 
              <span className="opacity-0 group-hover:opacity-100 transition-opacity"></span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );

  // 4. Minimalist
  const Minimalist = () => (
    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
      {items.map((item, index) => (
        <div key={index} className="relative aspect-square group overflow-hidden bg-gray-900">
          <img
            src={item.image}
            alt={item.title}
            className="w-full h-full object-cover opacity-80 group-hover:opacity-40 transition-opacity duration-500"
          />
          <div className="absolute inset-0 flex flex-col items-center justify-center p-4 translate-y-4 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-500">
            <h3 className="text-white text-xl font-bold text-center">{item.title}</h3>
            <span className="text-primary-400 text-sm mt-2">{item.category}</span>
          </div>
        </div>
      ))}
    </div>
  );

  // 5. Split Screen
  const SplitScreen = () => (
    <div className="space-y-24">
      {items.map((item, index) => (
        <div 
          key={index} 
          className={clsx(
            "flex flex-col lg:flex-row items-center gap-12 lg:gap-24",
            index % 2 === 1 && "lg:flex-row-reverse"
          )}
        >
          <div className="w-full lg:w-1/2">
            <div className="relative group overflow-hidden rounded-3xl">
              <img
                src={item.image}
                alt={item.title}
                className="w-full aspect-[4/3] object-cover transition-transform duration-700 group-hover:scale-105"
              />
              <div className="absolute inset-0 ring-1 ring-inset ring-black/10 rounded-3xl" />
            </div>
          </div>
          <div className="w-full lg:w-1/2 space-y-6">
            <span className="text-primary-600 font-bold tracking-widest uppercase text-sm">{item.category}</span>
            <h3 className="text-4xl md:text-5xl font-bold text-gray-900 leading-tight">{item.title}</h3>
            <p className="text-lg text-gray-600 leading-relaxed">{item.description}</p>
            <button className="px-8 py-4 bg-black text-white rounded-full font-bold hover:bg-primary-600 transition-colors">
              Exploire Project
            </button>
          </div>
        </div>
      ))}
    </div>
  );

  // 6. Carousel (Simplified for this component)
  const Carousel = () => (
    <div className="relative overflow-hidden rounded-[2.5rem] bg-gray-900 aspect-[21/9]">
      {/* This would typically use a slider library, but we'll build a simplified version */}
      <div className="absolute inset-0 flex">
        {items.slice(0, 1).map((item, index) => (
          <div key={index} className="relative w-full h-full">
            <img
              src={item.image}
              alt={item.title}
              className="absolute inset-0 w-full h-full object-cover opacity-60"
            />
            <div className="absolute inset-0 flex flex-col items-center justify-center text-center p-8">
              <span className="text-primary-400 font-bold uppercase tracking-[0.3em] mb-4 animate-fade-in-up">Featured Work</span>
              <h3 className="text-5xl md:text-7xl font-bold text-white mb-8 max-w-4xl">{item.title}</h3>
              <button className="px-10 py-5 border-2 border-white text-white rounded-full font-bold hover:bg-white hover:text-black transition-all">
                View Showcase
              </button>
            </div>
          </div>
        ))}
      </div>
      <div className="absolute bottom-10 left-1/2 -translate-x-1/2 flex gap-3">
        {items.slice(0, 3).map((_, i) => (
          <div key={i} className={clsx("w-3 h-3 rounded-full", i === 0 ? "bg-white" : "bg-white/30")} />
        ))}
      </div>
    </div>
  );

  // 7. Parallax (CSS Parallax)
  const Parallax = () => (
    <div className="space-y-32">
      {items.map((item, index) => (
        <div key={index} className="group">
          <div className="relative h-[60vh] overflow-hidden rounded-3xl">
            <img
              src={item.image}
              alt={item.title}
              className="w-full h-[120%] object-cover absolute -top-[10%] group-hover:scale-110 transition-transform duration-[2s] ease-out"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-transparent to-transparent flex flex-col justify-end p-12">
              <span className="text-primary-400 font-bold uppercase tracking-wider mb-2">{item.category}</span>
              <h3 className="text-4xl md:text-6xl font-bold text-white mb-4">{item.title}</h3>
              <p className="text-gray-300 max-w-2xl text-lg hidden md:block">{item.description}</p>
            </div>
          </div>
        </div>
      ))}
    </div>
  );

  // 8. Interactive Hover
  const InteractiveHover = () => (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-px bg-gray-200 border border-gray-200">
      {items.map((item, index) => (
        <div key={index} className="bg-white relative aspect-video overflow-hidden group">
          <img
            src={item.image}
            alt={item.title}
            className="w-full h-full object-cover grayscale group-hover:grayscale-0 group-hover:scale-110 transition-all duration-700"
          />
          <div className="absolute inset-0 p-12 flex flex-col justify-between opacity-0 group-hover:opacity-100 transition-opacity duration-500 bg-primary-600/90 text-white">
            <span className="text-sm font-bold uppercase tracking-widest">{item.category}</span>
            <div>
              <h3 className="text-3xl font-bold mb-4">{item.title}</h3>
              <p className="text-white/80 mb-6 line-clamp-2">{item.description}</p>
              <button className="flex items-center gap-2 font-bold group/btn">
                LEARN MORE 
                <span className="w-8 h-px bg-white group-hover/btn:w-12 transition-all" />
              </button>
            </div>
          </div>
        </div>
      ))}
    </div>
  );

  // 9. Vertical Timeline
  const Timeline = () => (
    <div className="relative max-w-4xl mx-auto before:absolute before:left-0 md:before:left-1/2 before:w-px before:h-full before:bg-gray-200">
      {items.map((item, index) => (
        <div 
          key={index} 
          className={clsx(
            "relative mb-24 md:mb-32 flex flex-col md:flex-row items-center",
            index % 2 === 1 ? "md:flex-row-reverse" : ""
          )}
        >
          <div className="absolute left-0 md:left-1/2 w-4 h-4 bg-primary-600 rounded-full -translate-x-1/2 z-10 border-4 border-white shadow-sm" />
          
          <div className={clsx(
            "w-full md:w-1/2 px-8 md:px-16",
            index % 2 === 1 ? "text-left md:text-right" : "text-left"
          )}>
            <span className="text-primary-600 font-bold text-sm mb-2 block">{item.year || '2024'}</span>
            <h3 className="text-2xl font-bold text-gray-900 mb-4">{item.title}</h3>
            <div className="relative aspect-video rounded-2xl overflow-hidden mb-4 md:hidden lg:block">
               <img src={item.image} alt={item.title} className="w-full h-full object-cover" />
            </div>
            <p className="text-gray-600">{item.description}</p>
          </div>
          <div className="hidden md:block w-1/2" />
        </div>
      ))}
    </div>
  );

  // 10. Bento Grid
  const BentoGrid = () => (
    <div className="grid grid-cols-1 md:grid-cols-4 gap-4 auto-rows-[240px]">
      {items.map((item, index) => (
        <div 
          key={index} 
          className={clsx(
            "relative group overflow-hidden rounded-[2rem] bg-gray-100",
            index === 0 && "md:col-span-2 md:row-span-2",
            index === 1 && "md:col-span-2",
            index === 4 && "md:col-span-2",
            index === 5 && "md:row-span-2"
          )}
        >
          <img
            src={item.image}
            alt={item.title}
            className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/20 to-transparent p-8 flex flex-col justify-end">
            <span className="text-primary-400 text-xs font-bold uppercase tracking-widest mb-2 opacity-0 group-hover:opacity-100 transition-opacity">
              {item.category}
            </span>
            <h3 className={clsx(
              "text-white font-bold transition-all transform",
              index === 0 ? "text-3xl" : "text-xl"
            )}>
              {item.title}
            </h3>
          </div>
        </div>
      ))}
    </div>
  );

  const variants = {
    grid: <GridClassic />,
    masonry: <MasonryLayout />,
    modern: <ModernCards />,
    minimal: <Minimalist />,
    split: <SplitScreen />,
    carousel: <Carousel />,
    parallax: <Parallax />,
    interactive: <InteractiveHover />,
    timeline: <Timeline />,
    bento: <BentoGrid />,
  };

  return (
    <section id={id || data?.id} className={clsx("py-20 bg-white", className, dataClassName)}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <SectionHeader />
        {variants[variant] || <GridClassic />}
      </div>
    </section>
  );
};

