import React from 'react';
import { ViewToggle as ViewToggleBase } from './ViewToggle';

export const ViewToggle = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ViewToggleBase, props)
  );
};
