import React from 'react';
import { PurchaseFlowNeo as PurchaseFlowNeoBase } from './PurchaseFlowNeo';

export const PurchaseFlowNeo = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseFlowNeoBase, props)
  );
};
