import React from 'react';
import { AssuranceStrip as AssuranceStripBase } from './AssuranceStrip';

export const AssuranceStrip = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(AssuranceStripBase, props)
  );
};
