import React from 'react';
import clsx from 'clsx';
import '../../styles/404.css';

/**
 * LostPageLoadingShell Component - Skeleton loader for error page components
 * @param {Object} props - Component props
 * @param {Object} props.data - Skeleton configuration data
 * @param {string} [props.data.variant='modern'] - Style variant: 'modern' | 'minimal' | 'classic' | 'split' | 'card' | 'image' | 'secret' | 'background' | '500' | '503'
 * @param {string} [props.className] - Additional CSS classes
 */
export const LostPageLoadingShell = ({
  data = {},
  className
}) => {
  const {
    variant = 'modern',
  } = data;

  // Container classes
  const containerClasses = {
    modern: 'page-not-found-modern min-h-screen bg-white',
    minimal: 'page-not-found-minimal min-h-screen bg-neutral-50',
    classic: 'page-not-found-classic min-h-screen bg-white',
    split: 'page-not-found-split min-h-screen bg-white',
    card: 'page-not-found-card min-h-screen bg-neutral-50',
    image: 'page-not-found-image min-h-screen bg-white',
    secret: 'page-not-found-secret min-h-screen bg-gradient-to-br from-purple-900 via-blue-900 to-indigo-900',
    background: 'page-not-found-background min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-black',
    500: 'page-not-found-500 min-h-screen bg-gradient-to-br from-red-50 to-orange-50',
    503: 'page-not-found-503 min-h-screen bg-gradient-to-br from-yellow-50 to-orange-50',
  };

  const renderSkeleton = () => {
    switch (variant) {
      case 'modern':
        return (
          <div className="min-h-screen bg-white flex items-center justify-center px-4 py-20">
            <div className="max-w-2xl mx-auto text-center w-full">
              {/* Large illustration placeholder */}
              <div className="mb-12">
                <div className="page-not-found-skeleton w-48 h-48 sm:w-64 sm:h-64 rounded-full mx-auto"></div>
              </div>

              {/* 404 Text */}
              <div className="mb-8 px-4">
                <div className="page-not-found-skeleton h-16 sm:h-20 w-32 sm:w-48 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-6 sm:h-8 w-48 sm:w-64 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-4 w-full max-w-[384px] mx-auto"></div>
              </div>

              {/* Action Buttons */}
              <div className="flex flex-col sm:flex-row gap-4 justify-center px-4">
                <div className="page-not-found-skeleton h-14 w-full sm:w-32 rounded-2xl"></div>
                <div className="page-not-found-skeleton h-14 w-full sm:w-32 rounded-2xl"></div>
              </div>

              {/* Additional Links */}
              <div className="mt-12 pt-8 px-4">
                <div className="page-not-found-skeleton h-4 w-32 mx-auto mb-4"></div>
                <div className="flex flex-wrap justify-center gap-4">
                  <div className="page-not-found-skeleton h-4 w-16 sm:w-20"></div>
                  <div className="page-not-found-skeleton h-4 w-20 sm:w-24"></div>
                  <div className="page-not-found-skeleton h-4 w-12 sm:w-16"></div>
                </div>
              </div>
            </div>
          </div>
        );

      case 'minimal':
        return (
          <div className="min-h-screen bg-neutral-50 flex items-center justify-center px-4">
            <div className="max-w-lg mx-auto text-center w-full">
              <div className="mb-8 px-4">
                <div className="page-not-found-skeleton h-16 sm:h-24 w-16 sm:w-24 rounded-full mx-auto mb-6"></div>
                <div className="page-not-found-skeleton h-12 sm:h-16 w-12 sm:w-16 rounded-full mx-auto mb-6"></div>
                <div className="page-not-found-skeleton h-16 sm:h-24 w-16 sm:w-24 rounded-full mx-auto"></div>
              </div>

              <div className="mb-12 px-4">
                <div className="page-not-found-skeleton h-6 sm:h-8 w-32 sm:w-48 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-4 w-full sm:w-64 mx-auto"></div>
              </div>

              <div className="space-y-4 px-4">
                <div className="page-not-found-skeleton h-14 w-full rounded-2xl"></div>
                <div className="page-not-found-skeleton h-14 w-full rounded-2xl"></div>
              </div>
            </div>
          </div>
        );

      case 'classic':
        return (
          <div className="min-h-screen bg-white">
            <div className="max-w-7xl mx-auto px-4 py-20 grid grid-cols-1 lg:grid-cols-2 gap-16 items-center">
              {/* Left side - Content */}
              <div className="order-2 lg:order-1 px-4">
                <div className="mb-8">
                  <div className="page-not-found-skeleton h-12 sm:h-16 w-24 sm:w-32 mb-6"></div>
                  <div className="page-not-found-skeleton h-6 sm:h-8 w-40 sm:w-48 mb-4"></div>
                  <div className="page-not-found-skeleton h-4 w-full mb-2"></div>
                  <div className="page-not-found-skeleton h-4 w-3/4 mb-8"></div>
                </div>

                <div className="flex flex-col sm:flex-row gap-4 mb-8">
                  <div className="page-not-found-skeleton h-12 w-full sm:w-40 rounded-lg"></div>
                  <div className="page-not-found-skeleton h-12 w-full sm:w-32 rounded-lg"></div>
                </div>

                <div>
                  <div className="page-not-found-skeleton h-6 w-48 mb-4"></div>
                  <div className="space-y-2">
                    <div className="page-not-found-skeleton h-4 w-32"></div>
                    <div className="page-not-found-skeleton h-4 w-40"></div>
                    <div className="page-not-found-skeleton h-4 w-36"></div>
                  </div>
                </div>
              </div>

              {/* Right side - Illustration */}
              <div className="order-1 lg:order-2 px-4">
                <div className="page-not-found-skeleton w-full h-64 sm:h-96 rounded-3xl"></div>
              </div>
            </div>
          </div>
        );

      case 'split':
        return (
          <div className="min-h-screen flex flex-col lg:flex-row">
            {/* Left side - Black background */}
            <div className="lg:w-1/2 bg-neutral-900 p-8 sm:p-12 flex flex-col justify-center">
              <div className="max-w-md mx-auto w-full px-4">
                <div className="mb-8">
                  <div className="page-not-found-skeleton bg-neutral-800 h-16 sm:h-20 w-24 sm:w-32 rounded mb-8"></div>
                  <div className="page-not-found-skeleton bg-neutral-800 h-6 sm:h-8 w-40 sm:w-48 rounded mb-6"></div>
                  <div className="page-not-found-skeleton bg-neutral-800 h-4 w-full rounded mb-2"></div>
                  <div className="page-not-found-skeleton bg-neutral-800 h-4 w-3/4 rounded mb-12"></div>
                </div>

                <div className="space-y-4">
                  <div className="page-not-found-skeleton bg-neutral-800 h-14 w-full rounded-2xl"></div>
                  <div className="page-not-found-skeleton bg-neutral-800 h-14 w-full rounded-2xl"></div>
                </div>
              </div>
            </div>

            {/* Right side - White background */}
            <div className="lg:w-1/2 p-8 sm:p-12 flex items-center justify-center">
              <div className="max-w-md w-full text-center px-4">
                <div className="mb-12">
                  <div className="page-not-found-skeleton w-32 h-32 sm:w-48 sm:h-48 rounded-full mx-auto mb-8"></div>
                  <div className="page-not-found-skeleton h-6 w-full sm:w-48 mx-auto mb-4"></div>
                  <div className="page-not-found-skeleton h-4 w-full max-w-xs mx-auto mb-8"></div>
                </div>

                <div className="page-not-found-skeleton h-14 w-full rounded-2xl mb-8"></div>

                <div className="pt-8">
                  <div className="page-not-found-skeleton h-4 w-32 mx-auto mb-4"></div>
                  <div className="space-y-2">
                    <div className="page-not-found-skeleton h-4 w-24 mx-auto"></div>
                    <div className="page-not-found-skeleton h-4 w-28 mx-auto"></div>
                    <div className="page-not-found-skeleton h-4 w-20 mx-auto"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );

      case 'card':
        return (
          <div className="min-h-screen bg-neutral-50 py-12 sm:py-20 px-4">
            <div className="max-w-4xl mx-auto">
              <div className="bg-white rounded-3xl shadow-xl overflow-hidden grid grid-cols-1 lg:grid-cols-2 min-h-[500px] sm:min-h-[600px]">
                {/* Left side - Content */}
                <div className="p-8 sm:p-12 flex flex-col justify-center">
                  <div className="mb-8">
                    <div className="page-not-found-skeleton h-12 sm:h-16 w-24 sm:w-32 mb-6"></div>
                    <div className="page-not-found-skeleton h-6 sm:h-8 w-48 sm:w-56 mb-4"></div>
                    <div className="page-not-found-skeleton h-4 w-full"></div>
                  </div>

                  <div className="space-y-4 mb-8">
                    <div className="page-not-found-skeleton h-14 w-full rounded-2xl"></div>
                    <div className="page-not-found-skeleton h-14 w-full rounded-2xl"></div>
                  </div>

                  <div className="pt-8">
                    <div className="page-not-found-skeleton h-4 w-20 mb-2"></div>
                    <div className="page-not-found-skeleton h-4 w-32"></div>
                  </div>
                </div>

                {/* Right side - Illustration */}
                <div className="bg-neutral-50 p-8 sm:p-12 flex items-center justify-center">
                  <div className="page-not-found-skeleton w-48 h-48 sm:w-64 sm:h-64 rounded-full mx-auto"></div>
                </div>
              </div>
            </div>
          </div>
        );

      case 'image':
        return (
          <div className="min-h-screen bg-white flex items-center justify-center px-4 py-20">
            <div className="max-w-2xl mx-auto text-center w-full">
              {/* Illustration placeholder */}
              <div className="mb-12">
                <div className="page-not-found-skeleton w-48 h-48 sm:w-64 sm:h-64 rounded-full mx-auto"></div>
              </div>

              {/* Content */}
              <div className="mb-8 px-4">
                <div className="page-not-found-skeleton h-6 sm:h-8 w-full sm:w-80 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-4 w-full max-w-lg mx-auto mb-2"></div>
                <div className="page-not-found-skeleton h-4 w-3/4 max-w-lg mx-auto mb-8"></div>
              </div>

              {/* Action Button */}
              <div className="px-4">
                <div className="page-not-found-skeleton h-14 w-full sm:w-48 rounded-2xl mx-auto"></div>
              </div>
            </div>
          </div>
        );

      case 'secret':
        return (
          <div className="min-h-screen bg-gradient-to-br from-purple-900 via-blue-900 to-indigo-900 flex items-center justify-center px-4 py-20">
            <div className="max-w-2xl mx-auto text-center w-full">
              {/* Large 404 */}
              <div className="mb-8 px-4">
                <div className="page-not-found-skeleton h-16 sm:h-24 w-32 sm:w-48 rounded mx-auto"></div>
              </div>

              {/* Content */}
              <div className="mb-12 px-4">
                <div className="page-not-found-skeleton h-6 sm:h-8 w-full sm:w-80 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-4 w-full max-w-lg mx-auto"></div>
              </div>

              {/* Action Button */}
              <div className="px-4">
                <div className="page-not-found-skeleton h-14 w-full sm:w-56 rounded-2xl mx-auto"></div>
              </div>
            </div>
          </div>
        );

      case 'background':
        return (
          <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-black flex items-center justify-center px-4 py-20">
            <div className="max-w-2xl mx-auto text-center w-full">
              {/* Content */}
              <div className="mb-12 px-4">
                <div className="page-not-found-skeleton h-10 sm:h-12 w-full sm:w-80 mx-auto mb-6"></div>
                <div className="page-not-found-skeleton h-4 w-full max-w-lg mx-auto mb-2"></div>
                <div className="page-not-found-skeleton h-4 w-3/4 max-w-lg mx-auto"></div>
              </div>

              {/* Action Button */}
              <div className="px-4">
                <div className="page-not-found-skeleton h-14 w-full sm:w-56 rounded-2xl mx-auto"></div>
              </div>
            </div>
          </div>
        );

      case '500':
        return (
          <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-50 flex items-center justify-center px-4 py-20">
            <div className="max-w-2xl mx-auto text-center w-full">
              {/* Large 500 */}
              <div className="mb-8 px-4">
                <div className="page-not-found-skeleton h-16 sm:h-24 w-32 sm:w-48 rounded mx-auto"></div>
              </div>

              {/* Content */}
              <div className="mb-12 px-4">
                <div className="page-not-found-skeleton h-6 sm:h-8 w-full sm:w-80 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-4 w-full max-w-lg mx-auto"></div>
              </div>

              {/* Action Buttons */}
              <div className="flex flex-col sm:flex-row gap-4 justify-center px-4">
                <div className="page-not-found-skeleton h-14 w-full sm:w-40 rounded-2xl"></div>
                <div className="page-not-found-skeleton h-14 w-full sm:w-32 rounded-2xl"></div>
              </div>
            </div>
          </div>
        );

      case '503':
        return (
          <div className="min-h-screen bg-gradient-to-br from-yellow-50 to-orange-50 flex items-center justify-center px-4 py-20">
            <div className="max-w-2xl mx-auto text-center w-full">
              {/* Large 503 */}
              <div className="mb-8 px-4">
                <div className="page-not-found-skeleton h-16 sm:h-24 w-32 sm:w-48 rounded mx-auto"></div>
              </div>

              {/* Content */}
              <div className="mb-12 px-4">
                <div className="page-not-found-skeleton h-6 sm:h-8 w-full sm:w-80 mx-auto mb-4"></div>
                <div className="page-not-found-skeleton h-4 w-full max-w-lg mx-auto"></div>
              </div>

              {/* Action Buttons */}
              <div className="flex flex-col sm:flex-row gap-4 justify-center px-4">
                <div className="page-not-found-skeleton h-14 w-full sm:w-40 rounded-2xl"></div>
                <div className="page-not-found-skeleton h-14 w-full sm:w-32 rounded-2xl"></div>
              </div>
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className={clsx(
      containerClasses[variant] || containerClasses.modern,
      className
    )}>
      {renderSkeleton()}
    </div>
  );
};
