import React from 'react';
import { PurchaseLoadingShell as PurchaseLoadingShellBase } from './PurchaseLoadingShell';

export const PurchaseLoadingShell = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseLoadingShellBase, props)
  );
};
