import React from 'react';
import { ContentsNavigator as ContentsNavigatorBase } from './ContentsNavigator';

export const ContentsNavigator = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(ContentsNavigatorBase, props)
  );
};
