import React from 'react';
import { PurchaseFlowPulse as PurchaseFlowPulseBase } from './PurchaseFlowPulse';

export const PurchaseFlowPulse = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(PurchaseFlowPulseBase, props)
  );
};
