import React from 'react';
import { BannerElite as BannerEliteBase } from './BannerElite';

export const BannerElite = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BannerEliteBase, props)
  );
};
