import React from 'react';
import { BannerFrost as BannerFrostBase } from './BannerFrost';

export const BannerFrost = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(BannerFrostBase, props)
  );
};
