import React from 'react';
import { RelatedRail as RelatedRailBase } from './RelatedRail';

export const RelatedRail = (props) => {
  const theme = props?.theme ?? props?.data?.theme ?? 'light';
  return React.createElement(
    'div',
    { 'data-theme': theme === 'dark' ? 'dark' : 'light' },
    React.createElement(RelatedRailBase, props)
  );
};
